import PlayerCard from '../playerCard';
import GameTable from "./gameTable";
import { useAppManager, useCampaignManager, usePlayerManager, useUiManager } from '../managerContexts';

import './gameRoom.scss';
import { APP_STATE } from '../../consts/states';
import BottomBar from './bottomBar';
import Chat from '../UI/chat';
import UiManager from '../../managers/uiManager';
import { NOTEBOOK_CATEGORY } from '../../models';
import { useEffect, useState } from 'react';
import FillBar from '../UI/fillBar';
import NeedleGauge from '../UI/needleGauge';
import StressInfo from './stressInfo';
import PlayerDisplay from './PlayerDisplay';




interface GameRoomProps {
	overviewMode?: boolean;
}

const GameRoom: React.FC<GameRoomProps> = ({overviewMode = false}) => {

	const appManager = useAppManager();
	const playerManager = usePlayerManager(); 
	const players = playerManager.getPlayers();
	const campaignManager = useCampaignManager();
	const uiManager = useUiManager();
	const partyManager = appManager.getPartyManager();

	const [partyExhaustion, setPartyExhaustion] = useState(partyManager.getPartyExhaustion());
	const [partyTension, setPartyTension] = useState(partyManager.getPartyTension());
	const [partyScore, setPartyScore] = useState(partyManager.getPartyScore());
	//const [campaignScore, getCampaignScore] = useState(campaignManager.getCampaignScore());

	useEffect(() => {
		const updateGameInfo = () => {
			setPartyExhaustion(partyManager.getPartyExhaustion());
			//getCampaignScore(campaignManager.getCampaignScore());
		}
		partyManager.on('dmScreenUpdated', updateGameInfo);
		return () => {
			partyManager.off('dmScreenUpdated', updateGameInfo);
		}
	}, [campaignManager]);
	
  useEffect(() => {
	const forceUpdate = () => {
	  setPartyTension(partyManager.getPartyTension());
	  setPartyScore(partyManager.getPartyScore());

	}
	uiManager.on('playerUpdate', forceUpdate);
	return () => {
	  uiManager.off('playerUpdate', forceUpdate);
	};

  }, [uiManager]);

  
	const handlePlayerSelect = (player: any) => {
		uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.PLAYER, player);
	}

	const playerRowDisplay = () => {
		return (
		<div className="gameroom-player-row">
			
			{players.map((player) => (
				<PlayerDisplay 
					key={player.id}
					player={player}
				/>
			))}
		</div>)
	}



  const backToOverview = () => {
	if(appManager.state === APP_STATE.SESSION_OVERVIEW) {
		appManager.state = APP_STATE.SESSION;
	} else {
		campaignManager.backToOverview();
	}
  }


  const setupMode = appManager.state === APP_STATE.SESSION_OVERVIEW;



  return (
	<div className = "gameroom-container">
		<div className="gameroom-top-container">
			
			<div className="player-display-container">

				<div className="player-row">
					{playerRowDisplay()}
					<div className="player-tension-holder">
						<div className="stress-needle">
							<StressInfo stress={partyTension} />
							<NeedleGauge value={partyTension/10} max={10} />
						<div className="stress-label-containers">
								<div className="stress-labels">
									<div className="stress-label">TENSION</div>
									<div className="stress-label">{partyTension}</div>
								</div>
								<div className="stress-labels right">
									<div className="stress-label">SCORE</div>
									<div className="stress-label">{partyManager.getTensionDescription2(partyTension)}</div>
								</div>
							</div>
						</div>
						
					</div>
				
				<div className="score-info">
          			<div className="score-label">PARTY SCORE</div>
					<div className="score-value">{partyScore}</div>	
				</div>
				</div>
				<div className="player-row-exhaustion">
					<div className="party-exhaustion">
						<div className="party-exhaustion-label">
							PARTY
							EXHAUSTION
						</div>
						<div className="party-exhaustion-label number">
							{partyExhaustion}
						</div>
					<FillBar totalSegments={20} filledSegments={partyExhaustion/5} theme="exhaustion" label="" />
					</div>
				</div>
			{/*  */}
			</div>
			
		</div>
		
		{!overviewMode && (<div className="gameroom-table-row-container">
			<div className = "gameroom-table-row">
				<GameTable />
			</div>
			{/* <BottomBar partyExhaustion={partyExhaustion} /> */}
		</div>
		)}

	</div>
  );
}

export default GameRoom;