import React, { useEffect, useMemo, useState } from 'react';
import './notebook.scss';
import CImage from './cimage';
import { useCampaignManager, useUiManager } from '../managerContexts';
import FillBar from './fillBar';
import { CAMPAIGNS, NB_EXPECTED_ENCOUNTER_TAGS } from '../../consts/campaigns';
import UiManager from '../../managers/uiManager';
import MoreInfo from './moreInfoButton';
import { EncounterEvent, NOTEBOOK_CATEGORY } from '../../models';
import { EventType } from '@testing-library/react';
import { ENCOUNTER_EVENT_TYPE } from '../../consts/general';
import CampaignDisplay from '../campaignDisplay';

const EncounterOverview: React.FC = () => {

    const campaignManager = useCampaignManager();
    const uiManager = useUiManager();
    const [encounter, setEncounter] = useState(campaignManager.getCurrentEncounter());
    const [expectedEncounterTags, setExpectedEncounterTags] = useState(campaignManager.getExpectedEncounterTags());
    const [establishedEncounterTags, setEstablishedEncounterTags] = useState(campaignManager.getResolvedTagsForBranch());
    const [locationTagsEstablished, setLocationTagsEstablished] = useState(campaignManager.getWeightedLocationTagsEstablished());
    const [locationTags, setLocationTags] = useState(campaignManager.getEncounterLocationTags());
    const [completedEvents,setCompletedEvents ] = useState(campaignManager.getCompletedEvents());
    const [campaignModalVisible, setCampaignModalVisible] = useState(false);
    const [canTakeBreak, setCanTakeBreak] = useState(campaignManager.canTakeBreak());
    const setSceneTags = useMemo(() => encounter?.location.tags.sort() || [], [encounter?.location.tags]);


    useEffect(() => {
        const updateEncounterState = () => {
            setCompletedEvents(campaignManager.getCompletedEvents());
            setExpectedEncounterTags(campaignManager.getExpectedEncounterTags());
            setEstablishedEncounterTags(campaignManager.getResolvedTagsForBranch());
            setLocationTags(campaignManager.getEncounterLocationTags());
            setLocationTagsEstablished(campaignManager.getWeightedLocationTagsEstablished());
            setCanTakeBreak(campaignManager.canTakeBreak());
            setEncounter(campaignManager.getCurrentEncounter());
        };
    
        campaignManager.on('encounterUpdated', updateEncounterState);

        return () => {
            campaignManager.off('updateEncounterState', updateEncounterState);
        };
    }, [encounter]);

    const sortedSetSceneTags = useMemo(() => {
        return [...setSceneTags].sort((a, b) => a.name.localeCompare(b.name));
    }, [setSceneTags]);
  
    if (!encounter) {
        return <div className="encounter-overview-container">No Encounter</div>;
    }
    const uniqueEventTypes = Array.from(new Set(encounter.events.map(event => event.type)));
    const currentLocationTheme = encounter.location.themes[0];
    const combinedTags = campaignManager.getTagsForEncounter(encounter);
    //const encounterBranches = encounter.branches;
 
    const useBreak = () => {
        campaignManager.useBreak();
        setCanTakeBreak(false);
    }

   


    const clickEvent = (index: number) => {
        campaignManager.encounterEventSelected(index);
        completedEvents.push(index);
        setCompletedEvents([...completedEvents]);
    }


    const getEventDescription = (eventType: ENCOUNTER_EVENT_TYPE) => {
        switch (eventType) {
           case ENCOUNTER_EVENT_TYPE.COMBAT:
            return 'Combat'; 
          
           case ENCOUNTER_EVENT_TYPE.ROLEPLAY:
            return 'Roleplaying'; 
          
           case ENCOUNTER_EVENT_TYPE.PUZZLE:
            return 'Puzzle'; 
          
           case ENCOUNTER_EVENT_TYPE.PERCEPTION:
            return 'Perception Check'; 
          
           case ENCOUNTER_EVENT_TYPE.TOWN:
            return 'Town Stuff'; 
          
           case ENCOUNTER_EVENT_TYPE.TRAVEL:
            return 'Travel'; 
          
           case ENCOUNTER_EVENT_TYPE.CHALLENGE:
            return 'Skill Challenge'; 
          
           case ENCOUNTER_EVENT_TYPE.TRAP:
            return 'Trap'; 
          
           case ENCOUNTER_EVENT_TYPE.CHARACTER_BACKSTORY:
            return 'Backstory'; 
          
           case ENCOUNTER_EVENT_TYPE.STORY:
            return 'Story'; 
          
           case ENCOUNTER_EVENT_TYPE.EXPLORATION:
            return 'Exploration'
        }
    }

    const eventsList = () => {
        if (encounter.events.length === 0) {
            return null;
        }
        return (
            <div className="encounter-eventlist">
                {encounter.events.map((event, index) => (
                    <div 
                        key={index} 
                        className={`event-entry ${event.type} ${completedEvents.includes(index) ? 'completed' : ''}`}
                        onClick={() => clickEvent(index)}
                    >
                        <div className="event-icon">
                            <CImage imageId={uiManager.getEventIcon(event.type)} />
                        </div>
                        <div className="event-name">{event.name}</div>
                        {/* <div className="event-description">{getEventDescription(event)}</div> */}
                       
                        {event.flavorText && (
                            <div className="event-flavor-text">{event.flavorText}</div>
                        )}
                    </div>
                ))}
            </div>
        );
    };

    
  const campaignDisplay = () => {
    return (
      <div className="session-control-campaign">
          <div className={`campaign-display header ${CAMPAIGNS[campaignManager.getCurrentCampaign() || 0].theme} icon-only`} onClick={() => setCampaignModalVisible(true)}> 
            <div className={'campaign-info left'}>
              <div className="campaign-info">{CAMPAIGNS[campaignManager.getCurrentCampaign() || 0].name}</div>
              <div className="campaign-info hooks">PLOT HOOKS</div>
              <div className="circle-display">
                {Array.from({ length: 4 }, (_, index) => (
                  <div
                    key={index+'circle'}
                    className={`circle ${index < campaignManager.getCampaignProgress() ? 'filled' : ''}`}
                  />
                ))}
              </div>
            </div>
            
            <div className="campaign-icon solo">
              <CImage imageId={CAMPAIGNS[campaignManager.getCurrentCampaign() || 0].icon} />
            </div>
          </div>
        </div>
    )
  }

  const getReadiness = (nbExpectedTags: number) => {
    if(nbExpectedTags=== NB_EXPECTED_ENCOUNTER_TAGS)    {
        return "WELL PREPARED";
    }else if(nbExpectedTags === NB_EXPECTED_ENCOUNTER_TAGS - 1) {
        return "READY";
    }else if(nbExpectedTags === NB_EXPECTED_ENCOUNTER_TAGS - 2) {
        return "INFORMED";
    }

    return "NOT READY";
  }
const hackToBranch = ( branch: any) => {
    campaignManager.advanceEncounter(branch);
}

const advanceEncounter = () => {
    campaignManager.advanceEncounter();
}

    return (
        <div className="encounter-overview-container">
           
            <div className="encounter-icon-container">
                <CImage imageId={encounter.icon} />
            </div>
            {!campaignModalVisible && (
                <div className={`encounter-overview-nonmodal `}>
                <div className = {`encounter-overview-break ${canTakeBreak ? '' : ' hidden'}`} onClick={useBreak}>
                    <div className="encounter-overview-break-label">
                        BREAK
                    </div>
                    <div className="encounter-overview-break-effect">
                        Reduce exhaustion, tension, attention
                    </div>
                </div>
            
                {campaignDisplay()}
                <div className="encounter-overview-contents">
                
            
                
                <div className = "encounter-overview-info-column">
                        <div className="encounter-overview-encounter-info">
                            <div className="encounter-header">
                                <MoreInfo callback={() => uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.ENCOUNTER, encounter)} classname="encounter"/>
                                {encounter.name}
                            </div>
                            {<FillBar totalSegments={establishedEncounterTags.length + expectedEncounterTags.length} filledSegments={establishedEncounterTags.length} theme={currentLocationTheme} label={`encounter elements`} />}
                            <div className="encounter-overview-encounter-info-tags-row">
                                
                                <div className="encounter-overview-encounter-info-tags">
                                    {establishedEncounterTags.map((tag, index) => (
                                        <div key={`established-${index}`} className={`encounter-flavor-tag text-pill type-location`}>
                                            {tag.name}
                                        </div>
                                    ))}  
                                </div>
                                <div className="encounter-overview-encounter-info-tags">
                                    {expectedEncounterTags.map((tag, index) => (
                                        <div key={`expected-${index}`} className={`encounter-flavor-tag text-pill`}>
                                            {tag.name}
                                        </div>
                                    ))}  
                                </div>
                            </div>
                        </div>
                    </div>
                
                    <div className = "encounter-overview-info-column">
                        <div className="encounter-overview-encounter-info">
                            <div className="encounter-header">
                                <MoreInfo callback={() => uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.ENCOUNTER, encounter)} classname="location"/>
                                {encounter.location.name}
                            </div>
                            <div className="encounter-overview-encounter-info">
                                {<FillBar totalSegments={locationTags.length} filledSegments={locationTagsEstablished} theme={currentLocationTheme} label={`LOCATION ELEMENTS`} />}
                                <div className="encounter-overview-encounter-info-tags-row">
                                    <div className="encounter-overview-encounter-info-tags">
                                        {sortedSetSceneTags.map((tag, index) => (
                                            <div key={index} className={`encounter-flavor-tag text-pill  ${campaignManager.getCollectedLocationTags().includes(tag) ? 'type-location': ''}`}>
                                                {tag.name}
                                            </div>
                                        ))}  
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="encounter-overview-events-container">
                    <div className="encounter-overview-event-header">
                        ENCOUNTER EVENTS
                    </div>
                    <div className="encounter-overview-events-row">
                        <div className="encounter-overview-legend">
                        {uniqueEventTypes.map((event, index) => (
                            <div key={index} className="event-legend-entry">
                                <div className="event-icon">
                                    <CImage imageId={uiManager.getEventIcon(event)} />
                                </div>
                                
                                <div className="event-name">{getEventDescription(event)}</div>
                                {/* <div className="event-description">{getEventDescription(event)}</div> */}
                            </div>
                        ))}
                        </div>
                        {eventsList()}
                    </div>
                </div>
            {encounter.branches && encounter.branches.length > 0 && (
                <div className="encounter-overview-bottom-container">
                    
                    
                        <div className="encounter-overview-upcoming-branches-container">
                            <div className={`encounter-overview-upcoming-branches-header`}>UPCOMING ENCOUNTERS</div>
                            <div className="encounter-overview-upcoming-upcoming-branches">
                                {encounter.branches.map((branch, index) => (
                                <div key={index} className={`encounter-overview-upcoming-branch text-theme-${branch.location.themes[0]}`}>
                                    <div className="encounter-overview-upcoming-branch-header">
                                    <div className="encounter-overview-upcoming-travel-header-text encounter" onClick={() => hackToBranch(branch)}>
                                                {branch.name}
                                        </div>
                                        <div className="encounter-overview-upcoming-travel-header-text">
                                                {branch.location.name}
                                        </div>
                                    </div>
                                        <FillBar totalSegments={4} filledSegments={Math.min(4 ,campaignManager.getExpectedTagsForBranch(branch.name).length)}  theme={branch.location.themes[0]} label={branch.name} />
                                        
                                
                                    <div className="encounter-overview-upcoming-branch-tags">
                                        {(() => {
                                            const expectedTags = campaignManager.getExpectedTagsForBranch(branch.name);
                                            const authoredTags = campaignManager.getAuthoredEncounterTags(branch.name).filter((tag) => !expectedTags.includes(tag));
                                            const totalSlots = NB_EXPECTED_ENCOUNTER_TAGS - (authoredTags.length + expectedTags.length);
                                            const fillerTags = Array(totalSlots)
                                              .fill({ name: "?" });
                                              return [...authoredTags.map((tag, index) => (
                                                <div key={`authored-${index}`} className={`encounter-flavor-tag text-pill expected`}>
                                                  {tag.name}
                                                </div>
                                              )),
                                              ...expectedTags.map((tag, index) => (
                                                <div key={`expected-tag-${index}`} className={`encounter-flavor-tag text-pill type-foreshadowing`}>
                                                {tag.name}
                                                </div>
                                            )), ...fillerTags.map((tag, index) => (
                                                <div key={index} className={`encounter-flavor-tag text-pill type-foreshadowing expected large-text`}>
                                                {tag.name}
                                            </div>
                                            ))]
                                        })()}
                                    </div>
                                    <div className="encounter-overview-upcoming-footer">
                                        <div className="encounter-overview-upcoming-travel-footer-text">
                                            {getReadiness(campaignManager.getExpectedTagsForBranch(branch.name).length)}
                                        </div>
                                    </div>
                                </div>
                                ))}
                            </div>
                        </div>
                        <div className="encounter-overview-advance-container">
                            <div className="encounter-overview-advance-text">ADVANCE ENCOUNTER</div>
                            <div className="encounter-overview-advance-button" onClick={advanceEncounter}>
                            <div className="encounter-overview-advance-button-text">PLAYERS CHOOSE</div>
                            <div className="encounter-overview-advance-info">{campaignManager.getEncounterAdvanceReadinessText()}</div>
                            </div>
                        </div>
                    </div>
                )}
                </div>
            )}
        
        {campaignModalVisible && (
            <CampaignDisplay
                campaign = {CAMPAIGNS[campaignManager.getCurrentCampaign() || 0]}
                collapsed = {false}
                onAccept = {() => setCampaignModalVisible(false)}
                acceptText = 'Close'
            />
        )}
        
        </div>
        
       
    );
};

export default EncounterOverview;