import React from 'react';
import './notebook.scss'; 
import { PlayerNotebookEntry } from '../../models';
import UiManager from '../../managers/uiManager';
import Schedule from '../schedule';
import CImage from './cimage';
import { useCharacterManager } from '../managerContexts';


interface NoteBookPlayerProps {
    playerInfo: PlayerNotebookEntry;
    singleColumn?: boolean;
}

const NotebookPlayer: React.FC<NoteBookPlayerProps> = ({ playerInfo, singleColumn }) => {

    const characterManager = useCharacterManager();

    const likesList = playerInfo.player.preferences.filter((preference) => preference[1] > 0);
    const hatesList = playerInfo.player.preferences.filter((preference) => preference[1] < 0);

    const getLikesList = () => {
        return likesList.map((preference, idx) => {
            return (
                <div key={idx} className="text-pill like"> 
                    <div className="notebook-text">
                        - {preference[0].name}
                    </div>
                </div>
            );
        });
    }

    const getHatesList = () => {
        return hatesList.map((preference, idx) => {
            return (
                <div key={idx} className="text-pill hate"> 
                    <div className="notebook-text">
                        - {preference[0].name}
                    </div>
                </div>
            );
        });
    }

    const getTraitsList = () => {
            return (playerInfo.player.traits.map((trait, idx) => (
                         <div key={idx}>
                                <div key={idx} className="text-pill trait">-{trait.name}</div>
                                 <div className="trait-description">
                                    {trait.description}
                                </div>
                        </div>
                        )));
    }

    return (
    <div className="notebook-player-container">
        {singleColumn && (
             <div className="title-text lower">
                {playerInfo.player.name}
         </div>
        )}
        {!singleColumn && (
       <div className = "player-notebook-left-content">
            <div className="notebook-player-content">
                <div className = "notebook-player-icon">
                    <CImage imageId={playerInfo.player.icon} />
                </div> 
                {/* <div className="notebook-text player-deets">
                    <div className="player-phone">
                        <b>Phone: </b> {UiManager.getPlayerPhone(playerInfo.player.name)}
                    </div>
                    <div className="player-email">           
                        <b>Email: </b>{UiManager.getPlayerEmail(playerInfo.player.name)}
                    </div>    
                </div> */}
            </div>    
            <div className="player-traits-container traits">
                
                {getTraitsList()}
            </div>
            {playerInfo.player.characters.length > 0 &&
            (
                <div className="player-characters notebook-text">           
                    <b>Characters</b>
                        {playerInfo.player.characters.map((character, idx) => 
                        {
                            const char = characterManager.getCharacterByIndex(character);
                        return (
                            <div key={idx} className="notebook-text">
                                - <b>{char.name}</b>: Level {char.level} {char.character_class} {char.race}
                            </div>
                        )}
                    )}
                </div>      
            )}
         
        </div>
         )}
        <div className = "player-notebook-right-content">
            <div className="player-preferences-container notebook-text">
               {singleColumn && (
                <div className="player-preferences traits">
                    <div className="player-preference-title-small traits">Player Traits</div>
                    {getTraitsList()}
                </div>
               )}
                <div className="player-preferences likes">
                    <div className="player-preference-title-small likes">Likes</div>
                    {getLikesList()}
                </div>
                <div className="player-preferences hates">
                <div className="player-preference-title-small hates">Hates</div>
                    {getHatesList()}
                </div>
            </div>
            <div className="player-schedule-container">
            {/* <div className="player-schedule-title">
                Availability
            </div> */}
            {/* <div className="player-schedule-legend">
                <Schedule 
                    dayData={[]}
                    legend={true}
                />
            </div>
            <Schedule dayData={playerInfo.player.schedule}/> */}
            </div>
        </div>
    </div>
    );
};

export default NotebookPlayer;




