

import { useEffect, useState } from 'react';
import { ATTRIBUTE_CONTRIBUTOR, ChatMessage, Encounter, HeroCombatant, Tag, FlavorTagSource, FlavorTagTheme } from '../../models';
import './challenge.scss';
import { useAppManager, useCampaignManager, usePlayerManager, useUiManager } from '../managerContexts';
import CImage from '../UI/cimage';
import AttributeDisplay from '../UI/attributeDisplay';
import ChallengeBar from './challengeBar';
import ChallengeDisplay from './challengeDisplay';
import PlayerManager from '../../managers/playerManager';
import { COST } from '../../consts/score';
import { ThemedButton } from '../UI/themedButton';

interface GameEncounterChallengeProps {
	encounter: Encounter;
}

  const GameEncounterChallenge: React.FC<GameEncounterChallengeProps> = ({encounter}) => {


	const resultsValues =[
		[0, 5, 10, 15, 25, 35, 40, 50, 75, 100],
		[5, 7, 10, 12, 15, 18, 22, 26, 30, 35],  
		[1 , 1 , 1, 2, 2, 3, 3, 3, 4, 5]
	   
	]
	const appManager = useAppManager();
	const partyManager = appManager.getPartyManager();
	const campaignManager = useCampaignManager();
	const uiManager = useUiManager();
	const chatManager = appManager.getChatManager();
	const playerManager = usePlayerManager();

	let locationTags = campaignManager.currentEncounter?.location.tags || [];
	const collectedLocationTags = campaignManager.getCollectedLocationTagsForChallenge();
	const [usedLocationTags, setUsedLocationTags] = useState<boolean[]>(new Array(locationTags.length).fill(false));
	const [selectedHeroes, setSelectedHeroes] = useState<HeroCombatant[]>([]);
	const [selectedLocationTags, setSelectedLocationTags] = useState<Tag[]>([]);
	const [heroes, setheroes] = useState<HeroCombatant[]>(partyManager.generateHeroes([]));
	const [playerAssignedTags, setPlayerAssignedTags] = useState<(Tag|null)[]>(new Array(heroes.length).fill(null));
    const [challengeResults, setChallengeResults] = useState(0);
	const [numFailures, setNumFailures] = useState(0);
	const [heroesEnabled, setHeroesEnabled] = useState(true);
	const [buttonKeys, setButtonKeys] = useState<{ [key: number]: number }>({});
	const challenge = campaignManager.currentEncounter?.challenge;
	const challengeTitle= (challenge && challenge[0]?.name) || "Skill Challenge";
	const challengeDescription = (challenge && challenge[0]?.description) || "You must overcome a challenge to proceed.";
	//const [succeeded, setSucceeded] = useState(false);

	const uncollectedLocationTags = locationTags.filter((tag) => 
		!collectedLocationTags.some(collectedTag => collectedTag.name === tag.name)
	  );

	
	const showLocationTag = (index: number) => {
		return playerAssignedTags[index];;
	}
	

	const regenerateAllButtons = () => {
		setButtonKeys({});
		for(let i = 0; i < heroes.length; i++) {
			regenerateButton(i);
		}
	}

	const regenerateButton = (index: number) => {
		setButtonKeys(prev => ({
		  ...prev,
		  [index]: Math.random()
		}));
	  };

	const canSelectLocationTag = (index: number) => {
		if(playerAssignedTags[index]){
			return true;
		}
		
		if(!selectedHeroes.includes(heroes[index])) 
			return false;
		
		if(collectedLocationTags.length <= selectedLocationTags.length){
			return false;
		}
		return true;
	}

	const selectRandomTag = (playerIndex: number) => {
		if(!canSelectLocationTag(playerIndex)) {
			return;
		}
		if(collectedLocationTags.length <= selectedLocationTags.length){
			return;
		}

		for(let i = 0; i < collectedLocationTags.length; i++) {
			const tag = collectedLocationTags[i];
			if(!selectedLocationTags.includes(tag) && !usedLocationTags[i]) {
				assignTagToPlayer(playerIndex, tag);
				const tagIndex = collectedLocationTags.findIndex(locationTag => locationTag.name === tag.name);
				handleSelectAssistTag(tagIndex);
				break;
			}
		}
	}

	const assignTagToPlayer = (playerIndex: number, tag: Tag) => {
		const newPlayerAssignedTags = [...playerAssignedTags];
		newPlayerAssignedTags[playerIndex] = tag;
		setPlayerAssignedTags(newPlayerAssignedTags);
		const random = Math.floor(Math.random() * 3);
		let chatMessage = `Could I use ${tag.name} here?`;
		switch(random) {
			case 0:
				chatMessage = `Oh! I'll use the ${tag.name} we were talking about earlier!`;
				break;
			case 1:
				chatMessage = `${tag.name} will give me a bit of an edge.`;
				break;
			case 2:
				chatMessage = `I think ${tag.name} will help me out here.`;
				break;
			default:
				chatMessage = `Could I use ${tag.name} here?`;
				break;
		}

		chatManager.playerMessage(chatMessage, playerIndex);
	}
	

	const handleSelectAssistTag = (index: number) => {

		const newUsedLocationTags = [...usedLocationTags];
		newUsedLocationTags[index] = !newUsedLocationTags[index];
		setUsedLocationTags(newUsedLocationTags);
		if(selectedLocationTags.includes(collectedLocationTags[index])) {
			setSelectedLocationTags(selectedLocationTags.filter(flavorTag => flavorTag.name !== collectedLocationTags[index].name));
		} else {
			setSelectedLocationTags([...selectedLocationTags, collectedLocationTags[index]]);
		}
	}

	const handleClickHeroAssist = (index: number, theme: string | null) => {
		const player = playerManager.getPlayerAssignedCharacter(index);
		const assisting = selectedHeroes.length > 0;
		console.log('Theme? ', theme);

		if(selectedHeroes.includes(heroes[index])) {
			setSelectedHeroes(selectedHeroes.filter(hero => hero.name !== heroes[index].name));
			if(playerAssignedTags[index]) {
				delectAssignedLocationTag(index);
			}
			if(player) {
				chatManager.playerMessage(`Actually, ${heroes[index].name} is going to sit this one out...`, player.id);
			}
		} else {
			setSelectedHeroes([...selectedHeroes, heroes[index]]);
			if(player) {
				if(assisting){
					if(theme){
						switch(theme) {
								case 'comedic':
								 chatManager.playerMessage( `Heehee...${heroes[index].name} will help.`);
								 break;
								case 'heroic':
								 chatManager.playerMessage( `${heroes[index].name} will assist.... FOR JUSTICE!`);
								 break;
								case `serious`:
								 chatManager.playerMessage( `${heroes[index].name} better help out. This is serious.`);
								 break;
								case `dramatic`:
								 chatManager.playerMessage( `To aid in your hour of need, ${heroes[index].name} will help!`);
								 break;
								case `grim`:
								 chatManager.playerMessage( `Maybe we will both die, but ${heroes[index].name} will help.`);
								 break;
								case `romantic`:
								 chatManager.playerMessage( `Maybe you want some company doing that...?  ${heroes[index].name} can help out.`);
								 break;
								case `fantasy`:
								 chatManager.playerMessage( `Like in all good stories, heroes stick together... ${heroes[index].name} will help!`);
								 break;
								case `horror`:
								 chatManager.playerMessage( `It gives me the chills, but ${heroes[index].name} will help anyway.`);
								 break;
								default:
								  
							  }
						}
						else{
							chatManager.playerMessage(`${heroes[index].name} can help!`, player.id);
						}
					} else {
					if(theme){
						switch(theme) {
								case 'comedic':
								 chatManager.playerMessage( `Heehee...${heroes[index].name} will do this, but while singing in a high-pitched voice.`);
								 break;
								case 'heroic':
								 chatManager.playerMessage( `${heroes[index].name} will do this FOR JUSTICE!`);
								 break;
								case `serious`:
								 chatManager.playerMessage( `Lets stop fooling around. ${heroes[index].name} will do this.`);
								 break;
								case `dramatic`:
								 chatManager.playerMessage( `For the glory of whatever we are doing, ${heroes[index].name} will do this.`);
								 break;
								case `grim`:
								 chatManager.playerMessage( `If ${heroes[index].name} dies doing this, so be it.`);
								 break;
								case `romantic`:
								 chatManager.playerMessage( `OooOooh. I think ${heroes[index].name} can handle this in style...`);
								 break;
								case `fantasy`:
								 chatManager.playerMessage( `${heroes[index].name} will do this with magic or something fantastical...`);
								 break;
								case `horror`:
								 chatManager.playerMessage( `There's something creepy about this, but ${heroes[index].name} will do it anyway.`);
								 break;
								default:
							  }
						}
						else{
							chatManager.playerMessage(`${heroes[index].name} will attempt this challenge!`, player.id);
						}
				}
				
			}
		}
	}

	useEffect(() => {
		chatManager.dmMessage(challengeDescription);
	}, [])

	const getPlayerUsedLocationTag = (index: number) => {
		return playerAssignedTags[index]?.name || "Select a Tag";
	}


	const isSelected = (index: number) => {
		return selectedHeroes.includes(heroes[index]);
	}

	const delectAssignedLocationTag = (playerIndex: number) => {
		// match the tag to the tag index and remove it from the selectedLocationTags
		const tag = playerAssignedTags[playerIndex];
		if(tag) {
			const newPlayerAssignedTags = [...playerAssignedTags];
			newPlayerAssignedTags[playerIndex] = null;
			setPlayerAssignedTags(newPlayerAssignedTags);
			setSelectedLocationTags(selectedLocationTags.filter(selectedTag => selectedTag.name !== tag.name));

			
			setUsedLocationTags(usedLocationTags.map((used, index) => {
				if(collectedLocationTags[index]?.name === tag.name) {
					return false;
				}
				return used;
			}));
		}
	}

	const sawResultCallback = () => {
		setSelectedLocationTags([]);
		setSelectedHeroes([]);
		setPlayerAssignedTags(new Array(heroes.length).fill(null));

		setHeroesEnabled(true);
		regenerateAllButtons();
		
	}

	const enableHerosCallback= (enable: boolean) => {
		setHeroesEnabled(enable);
	}

	const assistOrAttempt = (toUpper = false, buttonSelected = false) => {
		if(buttonSelected){
			return "DESELECT"
		}
		if(selectedHeroes.length === 0) {
			if(toUpper) {
				return "ATTEMPT";
			}
			return "Attempt";
		}

		if(toUpper) {
			return "ASSIST";
		}

		return "Assist";
	}

	const renderHeroes = () => {
		return(
		<div className="challenge-encounter-heroes">
				<div className="challenge-encounter-available-tags-title">
					Choose Hero to {assistOrAttempt()}
				</div>
				{heroes && heroes.map((hero, index) => {
					return (
						<div key={hero.name} className="challenge-encounter-hero-row">
							
							<div className = "challenge-encounter-hero-container">
								<div className={`challenge-encounter-hero ${isSelected(index) ? 'selected ' : ''} `}>
									<div className="challenge-encounter-hero-name">{hero.name}</div>
									<div className="challenge-character-icon">
										<CImage imageId={hero.icon} />
									</div>
									{ hero.character && hero.character.injury === 'injured' && (
										<div className="challenge-encounter-hero-injury"></div>
									)}
									{ hero.character && hero.character.injury === 'dead' && (
										<div className="challenge-encounter-hero-injury dead"></div>
									)}
								</div>
								<div className="challenge-encounter-hero-attributes">
									<AttributeDisplay hero={hero} category={ATTRIBUTE_CONTRIBUTOR.ALL} showAttributeLoss={true} />
								</div>
								<div className = "challenge-encounter-hero-ui">
									<div className="challenge-assist-column">
										<ThemedButton 
											className={`challenge-assist-button assist ${isSelected(index) ? 'selected ' : ' '} ${!heroesEnabled ? 'disabled': ''} `}
											regenerateKey={buttonKeys[index]} 
											onClick={(theme)=>handleClickHeroAssist(index, theme)}>
												{assistOrAttempt(true, isSelected(index))}
										</ThemedButton>
										<div className={`challenge-assist-button location ${canSelectLocationTag(index) ? '' : 'disabled'} ${!heroesEnabled ? 'disabled': ''}`}>
											<div className={`challenge-use-label text-pill use ${showLocationTag(index) ? 'hidden' : ''} `} onClick={() => {selectRandomTag(index)}} >inspiration</div>
											<div 
												className={`challenge-use-label text-pill  type-location applied-tag ${showLocationTag(index)? '' : 'hidden'}`}
												onClick={() => delectAssignedLocationTag(index)}
												>
													{getPlayerUsedLocationTag(index)}
												</div>
										</div>
									</div>
									
								</div>
							</div>
						</div>
					)})}	
				</div>
		);
	}

	
	const renderAvailableTags = () => {
		return(
		<div className="challenge-encounter-available-tags">
		
			Elements of {campaignManager.currentEncounter?.location.name}
			<div className="challenge-location-tags-container">
				{collectedLocationTags.map((flavorTag, index) => {
					return (
						<div key={flavorTag.name} className="challenge-location-tag">
							<div className={`challenge-location-tag-name text-pill type-location ${usedLocationTags[index] ? 'used' : ''}`} onClick = {() => handleSelectAssistTag(index)}>
								{flavorTag.name}
							</div>
						</div>
					)
				})}
				{uncollectedLocationTags.map((tag, index) => {
					return (
						<div key={tag.name} className="challenge-location-tag">
							<div className={`challenge-location-tag-name text-pill expected  ${usedLocationTags[index] ? 'used' : ''}`}>
								{tag.name}
							</div>
						</div>
					)
				})}
			</div>
			
		</div>
		);
	}

	const acceptResults = () => {
		partyManager.addPartyScore(resultsValues[0][challengeResults]);
		uiManager.showRewardModal(resultsValues[2][challengeResults], true);
	}

	const attemptCallback = (success: boolean, results: number, tag: Tag) => {


		//check for campaign chats
		const campaignChatTrigger = campaignManager.getEncounterChatTrigger(tag);
		if(!campaignManager.getEstablishedTags().includes(tag) &&  campaignChatTrigger) {
			 chatManager.fireNewChatMessage(campaignChatTrigger as ChatMessage);

		}else {

		const player = playerManager.getPlayerAssignedCharacter(selectedHeroes[0].character?.id || 0);
		if(player) {
			if(success) {
				if(selectedHeroes.length > 1) {
					chatManager.playerMessage(`This challenge is no match for our combined skills!`, player.id);
				}else{
					if(Math.random() < 0.25) {
						chatManager.playerMessage(`Boom. Success. NEXT Challenge.`, player.id);
					} else {
						chatManager.playerMessage(`Yeah!`, player.id);
					}
				}
			} else {
				if(selectedHeroes.length > 1) {
					chatManager.playerMessage(`Even with our combined skills we still failed?!`, player.id);
				}else{
					if(Math.random() < 0.25) {
						chatManager.playerMessage(`NOOOO. Oooooo. Ooo.`, player.id);
					} else {
						chatManager.playerMessage(`Maybe someone else should try...`, player.id);
					}
				}
			}
		}
	}

		// Set all assigned tags as used
		partyManager.exhaustParty(selectedHeroes.length * COST.EXHAUSTION.CHALLENGE_PER_HERO);
	//	setHeroesEnabled(true);
		if(success) {
			setChallengeResults(challengeResults + results)
			
		}else {
			setNumFailures(numFailures + 1)
			partyManager.addPartyTension(resultsValues[1][challengeResults]);
		}
		// Kick off animation
	}
const getChallengeTags = () => {

	const thisEvent = campaignManager.getCurrentEvent();
	if(thisEvent && thisEvent.tags) {
		return thisEvent.tags;
	}

	return campaignManager.getEncounterTags();
}

	  return (

		<div className="game-encounter-challenge">
		<div className="challenge-container">
			<div className="challenge-top-bar">
				<div className="encounter-title">{challengeTitle}</div>
				<div className="encounter-title desc">{challengeDescription}</div>
		  	</div>
			
			<div className="challenge-body">
				<div className="challenge-character-list">
					{renderAvailableTags()}
					{renderHeroes()}
				</div>
				<div className="challenge-display-container">
					
					<ChallengeDisplay 
						challengeTags={getChallengeTags()}
						challengeAssistTags={selectedLocationTags}
						challengeHeroes={selectedHeroes}
						attemptCallback={attemptCallback}
						sawResultsCallback={sawResultCallback}
						heroesEnabledCallback={enableHerosCallback}
					/>
				</div>
			
			</div>
			
			<div className="">			
			</div>
			
			
			
			
			<div className="challenge-footer">
				<div className="challenge-reward-track">
					<ChallengeBar 
						results = {challengeResults}
						resultsValues = {resultsValues}
						/>
				</div>
				<div className="challenge-rewards-ui-container">
					<div className="challenge-failure">
						<div className="challenge-failure-title">
						Failures
						</div>
						<div className="challenge-display-failures">
							
							{Array.from({ length: 3 }).map((_, index) => (
								<span
									key={index}
									className={`failure-circle ${index < numFailures ? 'filled' : ''}`}
								></span>
							))}	
						</div>
					</div>
					<div className="challenge-accept-results" onClick={() => acceptResults()}>
						ACCEPT RESULTS
					</div>
				</div>
			</div>
		</div>
		</div>
	  );
	};

export default GameEncounterChallenge;