import { useEffect, useState } from "react";
import { useAppManager, useCampaignManager, useUiManager } from "../managerContexts";
import FillBar from "../UI/fillBar";
import { APP_STATE } from "../../consts/states";
import NeedleGauge from "../UI/needleGauge";
import Chat from "../UI/chat";
import ToneTracker from "./toneTracker";
import { THEME_FILLED_REQ } from "../../consts/general";

interface BottomBarProps {
    
}


const BottomBar: React.FC<BottomBarProps> = ({}) => {

    const appManager = useAppManager();
    const uiManager = useUiManager();
    const campaignManager = useCampaignManager();
    const [flavorTagThemes, setFlavorTagThemes] = useState(campaignManager.getCollectedThemes());
    
    
    useEffect(() => {
        const updateFlavorTags = () => {
          setFlavorTagThemes({ ...campaignManager.getCollectedThemes() });
        };
    
        campaignManager.on('dmScreenUpdated', updateFlavorTags);
    
      
        return () => {
          campaignManager.off('dmScreenUpdated', updateFlavorTags);
        };
      }, [campaignManager]);

       const backToOverview = () => {
        if(appManager.state === APP_STATE.SESSION_OVERVIEW) {
            appManager.state = APP_STATE.SESSION;
        } else {
            campaignManager.backToOverview();
        }
    }

    const changeBG = () => {
        uiManager.toggleImageForEvent();
    }
  const redeemTheme = (theme: string) => {
    campaignManager.redeemTheme(theme);
  }
  const toneString = 'TABLE TONE' + (Object.entries(flavorTagThemes).length > 1 ? 'S' : '');

  const shouldShowChat = () => {
    if (appManager.state === APP_STATE.SESSION || appManager.state === APP_STATE.SESSION_OVERVIEW) {
      return true;
      
    } 
    return false;
  }


    return (
        <div className={`bottom-bar-container ${shouldShowChat() ? '' : 'hidden'}`}>
            <div className = "bottom-bar">
                <Chat />
                {Object.entries(flavorTagThemes).length > 0 && (
                    <ToneTracker 
                        themes={flavorTagThemes}
                        maxSegments={THEME_FILLED_REQ}
                        onRedeem={redeemTheme}
                    />
                    )}
                </div>
            <div className="bottom-bar-controls">
                <div className="gameroom-party-info-stress">
                  
                </div>
                <div className = "gameroom-party-info">    
                   
			    </div>
                <div className = 'gameroom-nav-button-container'>
                    <div className='gameroom-nav-button nav-button' onClick={backToOverview}>
                        RUN SESSION
                    </div>
                    <div className='gameroom-nav-button nav-button' onClick={changeBG}>
                        Alt BG
                    </div>
                </div>
            </div>    
        </div>
    );
}
export default BottomBar; 