import React from 'react';
import { FlavorTagTheme } from '../../models';

interface ToneTrackerProps {
  themes: Record<string, number>;
  maxSegments: number;
  onRedeem: (theme: string) => void;
}

const ToneTracker: React.FC<ToneTrackerProps> = ({ themes, maxSegments, onRedeem }) => {
  return (
    <div className="tone-tracker">
      <div className="tone-header">
        <span className="tone-title">Session Themes</span>
       
      </div>
      <div className="tone-grid">
        {Object.entries(themes).map(([theme, count]) => (
          <div 
            key={theme} 
            className={`tone-item theme-${theme} muted ${count >= maxSegments ? 'active' : ''}`} 
            onClick={count >= maxSegments ? () => onRedeem(theme) : undefined}
          >
            <div className="tone-item-header">
              <span className="tone-name">{theme}</span>
              <span className="tone-count">{count}/{maxSegments}</span>
            </div>
            <div className="tone-progress-container">
              <div 
                className={`tone-progress-bar theme-${theme}`}
                style={{ width: `${(count / maxSegments) * 100}%` }}
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ToneTracker;