import { Tag } from "../../models";

interface TooltipProps {
  theme: string;
  tag: Tag;
  message: string;
}

const Tooltip: React.FC<TooltipProps> = ({ theme, tag, message }) => {
  return (
    <div className={`tooltip theme-${theme}`}>
      <div className="tooltip-content">
        {message}
      </div>
    </div>
  );
};

export default Tooltip;