

import { useState, useRef, useEffect } from 'react';
import { Encounter, RPEncounterFlavorTag, FlavorTagSource, Tag, ATTITUDE } from '../../models';
import { useAppManager, useCampaignManager, useCharacterManager, usePlayerManager, useUiManager } from '../managerContexts';
import FillBar from '../UI/fillBar';
import PartyManager from '../../managers/partyManager';
import CImage from '../UI/cimage';
import CharacterManager from '../../managers/characterManager';
import PlayerManager from '../../managers/playerManager';



interface GameEncounterRoleplayProps {
	encounter: Encounter;
}

const bandLabels = [
	{ text: "Organic Conversation +20 Score", top: "4%" },
	{ text: "Guided Interactions +10 Score", top: "39%" },
	{ text: "Off Topic +0 Score", top: "74%" }
  ];

  const GameEncounterRoleplay: React.FC<GameEncounterRoleplayProps> = ({encounter}) => {

	const uiManager = useUiManager();
	const campaignManager = useCampaignManager();
	const appManager = useAppManager();
	const chatManager = appManager.getChatManager();
	const partyManager = appManager.getPartyManager();
	const characterManager = useCharacterManager();
	const playerManager = usePlayerManager();
	const [positions, setPositions] = useState<{ top: string; left: string }[]>([]);
	const [flavorTags, setFlavorTags] = useState<RPEncounterFlavorTag[]>(() => campaignManager.generateRoleplayEventTags('init'));
	const [freeTags, setFreeTags] = useState<number>(5);

	const containerRef = useRef<HTMLDivElement>(null);
	
	useEffect(() => {
		const newPositions: { top: string; left: string }[] = [];
		const maxRetries = 100;
		const bands = [
		  { min: 5, max: 20 },  // Top band
		  { min: 40, max: 55 },  // Middle band
		  { min: 77, max: 88 }   // Bottom band
		];
		
		const isOverlap = (pos: { top: string; left: string }, top: string, left: string) => 
			Math.abs(parseFloat(pos.top) - parseFloat(top)) < 10 &&
			Math.abs(parseFloat(pos.left) - parseFloat(left)) < 15;

		flavorTags.forEach(() => {
		  let currentTry = 0;
		  let top: string, left: string;
		  let overlap;
		  do {
			currentTry++;
			const bandIndex = Math.floor(Math.random() * 3);
			const band = bands[bandIndex];
			top = `${band.min + Math.random() * (band.max - band.min)}%`;
			left = `${Math.random() * 80}%`;
			// eslint-disable-next-line no-loop-func
			overlap = newPositions.some((pos) => isOverlap(pos, top, left));
		  } while (overlap && currentTry < maxRetries);
		  newPositions.push({ top, left });
		});
	  
		setPositions(newPositions);
	  }, [flavorTags]);
	

	  const getTooltipText = (rpFlavorTag: RPEncounterFlavorTag) => {
		let tooltipText = '';

		


		if(rpFlavorTag.tag) {

			if(campaignManager.getCampaignTooltipForTag(rpFlavorTag.tag)) {
				return campaignManager.getCampaignTooltipForTag(rpFlavorTag.tag );
			}


			const flavorTag = rpFlavorTag.tag;
			const playerLikes = playerManager.getPartyAttitudeForTag(flavorTag.tag);
		switch(flavorTag.source) {
			case FlavorTagSource.LOCATION:
				switch(Math.floor(Math.random() * 2)) {
					case 0:
						tooltipText =  `The players interact with ${flavorTag.tag.name}`;
						break;
					case 1:
						tooltipText = `You describe some nearby ${flavorTag.tag.name}`;
						break;
				}
				
				break;
			case FlavorTagSource.FORESHADOWING:
				if(playerLikes === ATTITUDE.LIKE) {
					tooltipText =  `Some players will be happy to know that ${flavorTag.tag.name} seems to be part of an upcoming encounter`;
				}else if(playerLikes === ATTITUDE.HATE) {
					tooltipText =  `Much to one player's annoyaince - you imply that ${flavorTag.tag.name} will be expected in an upcoming encounter`;
				}else {
					tooltipText =  `You foreshadow ${flavorTag.tag.name} for an upcoming encounter`;
				}
				break;
			case FlavorTagSource.FORESHADOWING_RANDOM:
					if(playerLikes === ATTITUDE.LIKE) {
						tooltipText =  `The players like ${flavorTag.tag.name}...add it on a whim to an upcoming encounter`;
					}else if(playerLikes === ATTITUDE.HATE) {
						tooltipText =  `${flavorTag.tag.name} will feature in an upcoming encounter even though some players hate ${flavorTag.tag.name}`;
					}else {
						tooltipText =  `Ad-lib ${flavorTag.tag.name} into an upcoming encounter`;
					}
					break;
			case FlavorTagSource.RUMOR:
				// check if this is an expected tag for a branch
				const isExpectedForCurrentEncounter = campaignManager.getExpectedEncounterTags().includes(flavorTag.tag);
				tooltipText =  
					isExpectedForCurrentEncounter ? `You reinforce the importance of ${flavorTag.tag.name}.` :
				  									`You confirm the players' questions about ${flavorTag.tag.name}`;
				break;
			case FlavorTagSource.PLAYERS:
				tooltipText =  `The players have feelings about ${flavorTag.tag.name}`;
				break;
			case FlavorTagSource.QUESTS:
				tooltipText =  `A quest may be advanced bacause of ${flavorTag.tag.name}`;
				break;
			case FlavorTagSource.ALL:
				tooltipText =  `For no specific reason, the players discuss ${flavorTag.tag.name}`;
				break;

			case FlavorTagSource.MAGIC_ITEM:
				tooltipText =  `You award the players the ${flavorTag.tag.name} they have been looking for!`;
				break;
			case FlavorTagSource.BACKSTORY:
				tooltipText =  `The ${flavorTag.tag.name} is integral to a character's backstory!`;
				break;
			case FlavorTagSource.PLOT:
				tooltipText =  `${flavorTag.tag.name} will be important to the story`;
				break;
			default:
				tooltipText = '';
				break;
		}	

		tooltipText += chatManager.getThemePostFix(flavorTag);
	
		
		return tooltipText;
	}else if(rpFlavorTag.character) {
		tooltipText = `Spend some time focusing on ${rpFlavorTag.character.name}`;
		return tooltipText;
	}
}

	useEffect(() => {
		const adjustTooltips = () => {
		  const container = containerRef.current;
		  if (!container) return;
	
		  const containerRect = container.getBoundingClientRect();
		  const tooltips = container.querySelectorAll('.encounter-flavor-tag');
	
		  tooltips.forEach((tooltip) => {
			
			const tooltipText = tooltip.querySelector('.tooltip-text') as HTMLElement;
			const tooltipRect = tooltipText.getBoundingClientRect();
	
			if (tooltipRect.right > containerRect.right) {
			  tooltipText.style.left = 'auto';
			  tooltipText.style.right = '0';
			  tooltipText.style.transform = 'translateX(0)';
		
			} else if (tooltipRect.left - tooltipRect.width/2 < 0) {
			  tooltipText.style.left = '0';
			  tooltipText.style.right = 'auto';
			  tooltipText.style.transform = 'translateX(0)';
			
			
			} else {
				
			  tooltipText.style.left = '50%';
			  tooltipText.style.right = 'auto';
			  tooltipText.style.transform = 'translateX(-50%)';
			
			}
	
			if (tooltipRect.bottom > 0){// containerRect.bottom/2) {
			  tooltipText.style.top = 'auto';
			  tooltipText.style.bottom = '100%';
		
			} else {
			  tooltipText.style.top = '100%';
			  tooltipText.style.bottom = 'auto';
			 
			}
		  });
		};
	
		setTimeout(adjustTooltips, 1000);
		window.addEventListener('resize', adjustTooltips);
		return () => window.removeEventListener('resize', adjustTooltips);
	  }, [positions, flavorTags]);


	  const getScoreForPosition = (position: string): number => {
		const topPercentage = parseFloat(position);
		
		// Check which band the tag is in
		if (topPercentage <= 20) {
		  return 20; // Top band - Organic Conversation
		} else if (topPercentage <= 55) {
		  return 10; // Middle band - Guided Interactions
		} else {
		  return 0;  // Bottom band - Off Topic
		}
	  };


	  const handleTagClick = (flavorTag: RPEncounterFlavorTag, index: number) => {
		
		const tagPosition = positions[index];
		const score = getScoreForPosition(tagPosition.top);

		setFreeTags(freeTags - 1);
	
		if(freeTags === -2) {
			chatManager.playerMessage('YAWWWN');
		} else {
			if(freeTags > 0) {
				partyManager.addPartyScore(score);
			}

			chatManager.roleplayingEvent(flavorTag);
		}
		campaignManager.selectRoleplayFlavorTag(flavorTag, freeTags);
		setFlavorTags(campaignManager.generateRoleplayEventTags(''));
	  };

	  const instructions = freeTags > 0 ? `The players engage the locals in conversation in Sagewood Tavern.` : `You may keep roleplaying, but your players are growing bored.`;
	  const getPlayerAttitudesStyle = (tag: Tag) => {
		return uiManager.getPlayerAttitudesStyle(tag);
	  }


	const pressHurryButton = () => {
		if(freeTags > 0) {
			partyManager.addPartyTension(10);
			setFreeTags(freeTags - 1);
			
			chatManager.dmMessage(['Lets moooove along!', 'Blah Blah blah', 'Theres a lot more game to get to.']);
			setFlavorTags(campaignManager.generateRoleplayEventTags(''));
		}
	}

		const getLegend = () => {
			const characters = characterManager.getCharacters();
		  
			return (
			  <div className="roleplay-legend">
				<div className="legend-section">
				  <div className="legend-row">
					<div className="sample-tag text-pill type-location">Location</div>
					<div className="sample-desc">Describe the environment</div>
				  </div>  
				  <div className="legend-row">
					<div className="sample-tag text-pill type-encounter">Foreshadow</div>
					<div className="sample-desc">Hint about an upcoming encounter</div>
				  </div>
				  <div className="legend-row">
					<div className="sample-tag text-pill type-all">New Topic</div>
					<div className="sample-desc">Introduce/entertain new ideas</div>
				  </div>
				  <div className="legend-row">
					<div className="sample-tag text-pill type-rumor">Resolve Topic</div>
					<div className="sample-desc">Integrate/confirm introduced topics</div>
				  </div>
				</div>
		  
				<div className="legend-section">
				  <div className="legend-column">
					
					<div className="legend-watchfor-container">
						<div className="sample-tag text-pill type-backstory">Backstory</div>
						<div className="sample-tag text-pill type-magic-item">Magic Item</div>
						<div className="sample-tag text-pill type-plot-hook">Plot Hooks</div>
					</div>
					
				  </div>
				 
				</div>
		  
				<div className="legend-section attitudes">
					<div className="legend-player-row">
						<div className="legend-player-column">
							<div className="legend-title">Player Attitudes:</div>
							<div className="attitude-row">
								<span className="attitude like">Like</span>
								<span className="attitude hate">Hate</span>
								<span className="attitude neutral">Mixed</span>
							</div>
						</div>
						<div className="legend-player-column">
							
								<div className="encounter-rp-character-container">
									<div className="legend-player-row">
										{characters.map((character) => (
											<CImage key={character.id} imageId={character.icon} />
										))}
									</div>
								</div>
							
							<div className="legend-character-info">+2/-1 Attention</div>
						</div>
					</div>
				</div>
			  </div>
			);
		  };
	



	  return (
		<div className="encounter-roleplay-container">
			<div className="encounter-roleplay-header">
		  		<div className="encounter-roleplay-tutorial">
					{getLegend()} 
		  		</div>
				<div className = "encounter-roleplay-text">
					<div className="encounter-roleplay-title">
						Roleplay in {encounter.location.name}
					</div>
					<div className="encounter-roleplay-instructions">
						{instructions}
					</div>
				</div>
				<div className="encounter-roleplay-skill-container">
					<FillBar totalSegments={5} filledSegments={freeTags} theme="engagement" label="Party Patience" />
					<div className={`encounter-roleplay-hurry-button ${freeTags <= 0 ? 'disabled' : ''}`} onClick={() => pressHurryButton()}>Hurry Players</div>
					<div className="encounter-roleplay-hurry-text">{freeTags > 0 ? "+10 Tension" : ""}</div>
				</div>
		  </div>
		  <div className= {`encounter-roleplay-flavor-tags-container  ${freeTags <= 0 ? 'disabled' : ''} `} ref={containerRef}>
			{bandLabels.map((label, index) => (
				<div 
				key={`band-label-${index}`}
				className={`band-label ${freeTags <= 0 ? 'disabled' : ''} `}
				style={{ top: label.top }}
				>
				{label.text}
				</div>
			))}
			{flavorTags.map((flavorTag, index) => (
				flavorTag.tag ? (
				<div
				key={index}
				className={
					
						`encounter-flavor-tag text-pill theme-${flavorTag.tag.theme} 
						type-${flavorTag.tag.source} 
						${getPlayerAttitudesStyle(flavorTag.tag.tag)}`
				}
				style={positions[index]}
				data-tooltip={`${getTooltipText(flavorTag)}`} // Add tooltip text here
				onClick={() => handleTagClick(flavorTag, index)}
				>
				{flavorTag.tag?.tag?.name}
				<div className="tooltip-text">{getTooltipText(flavorTag)}</div>
				</div>
			) : 
			(
				<div
					key={index}
					className={`encounter-rp-character theme-character`}
					style={positions[index]}
					data-tooltip={`${getTooltipText(flavorTag)}`} // Add tooltip text here
					onClick={() => handleTagClick(flavorTag, index)}
					>
					<div className="encounter-rp-character-container">
						<CImage imageId={flavorTag.character?.icon}/>
						<div className="encounter-rp-character-name">{flavorTag.character?.name}</div>
					</div>
					
					<div className="tooltip-text">{getTooltipText(flavorTag)}</div>
				</div>
			)
		)
		
		
		)}
			</div>
		</div>
	  );
};



export default GameEncounterRoleplay;