import React, { useEffect, useRef, useState } from 'react';
import './chat.scss';
import CImage from './cimage';
import { CHAT_TYPE, ChatMessage } from '../../models';
import ChatMessageDisplay from './chatmessagedisplay';
import {randomChats} from '../../consts/randomChats';
import { useAppManager } from '../managerContexts';
import exp from 'constants';

interface ChatProps {
 // expanded: boolean; 
}

const Chat: React.FC<ChatProps> = ({}) => {

  const containerRef = useRef<HTMLDivElement>(null);

  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [expanded, setExpanded] = useState(false);
  const [newMessage, setNewMessage] = useState<ChatMessage | null>(null);
  const [muted, setMuted] = useState(false);

  const appManager = useAppManager();

  const chatManager = appManager.getChatManager();

  useEffect(() => {
    if (expanded && containerRef.current) {
      containerRef.current.scrollTop = containerRef.current.scrollHeight;
    }
  }, [newMessage, expanded]);

  useEffect(() => {
    const handleNewMessage = (newMessage: ChatMessage) => {
      setMessages((prevMessages) => [...prevMessages, newMessage]);
      setNewMessage(newMessage);
    };
    chatManager.on('chat-message', handleNewMessage);
    return () => {
      chatManager.off('chat-message', handleNewMessage);
    };
  }, [chatManager]);


  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     const newMessage = chatManager.getFlavorChat();
  //     setMessages([...messages, newMessage]);
  //   }, chatManager.getRandomChatInterval());
  //   return () => clearInterval(interval);
  // }, [chatManager, messages]);

  // useEffect(() => {
  //   if (!expanded && messages.length > 0) {
  //     const latestMessage = messages[messages.length - 1];

  //     if(latestMessage.speaker.type !== CHAT_TYPE.SYSTEM) {
  //       setNewMessage(latestMessage);
  //     }

  //     const timer = setTimeout(() => {
  //       setNewMessage(null);
  //     }, 5000); 

  //     return () => clearTimeout(timer);
  //   }
  // }, [messages, expanded]);

  const toggleChat = () => {
      setExpanded(!expanded);
  };

  const toggleMute = () => {
    setMuted(!muted);
    if(expanded) {
      setExpanded(false);
    }
  }


  const renderExpandedChat = () => {

    if(!muted && expanded && messages.length !== 0) {
    return (
      <div className="chat-expanded">
        <div className="chat-expanded-container" ref={containerRef}>
          <div className="chat-expanded-messages">
            {messages.map((chatmessage, index) => {
              return (
                <div key={index} className="chat-message-container">
                  <ChatMessageDisplay
                    chatMessage= {chatmessage}/>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    )
  }
  return null;
}

const renderNewMessage = () => {
  if(muted) {
    return <div className="chat-muted-message">MUTED</div>;
  }
  
  if(!muted && newMessage) {
    return (
      <div className="chat-new-message">
        <ChatMessageDisplay chatMessage={newMessage} newMessage={!!newMessage}/>
      </div>
    )
  }
  return null;
}

const renderChatControls = () => {
  return (
    <div className="chat-controls">

     


      <div className={`chat-toggle open ${expanded ? 'expanded' : 'collapsed'}`}>
        <div className="chat-toggle-icon" onClick={toggleChat}>
          <div className = "chat-messages-count-container">
            <div className="chat-toggle-text open">{expanded ? "-" : "≡"}</div>
          </div>
        </div>
      </div>


      <div className={`chat-toggle mute ${muted ? 'muted' : ''}`}>
        <div className={`chat-toggle-text`} onClick={toggleMute}>
          {muted ? 'O' :'X' }
        </div>
      </div>

    </div>
  )
}



  return (
    <div className={`chat-exterior ${expanded ? 'expanded' : ''}`}>
      <div className="chat-container">
        <div className="new-message-container">
          {renderNewMessage()}
        </div>
        {renderChatControls()}
      </div>
      {renderExpandedChat()}
    </div>
    
  );
}

export default Chat;

