import React from 'react';
import './gameRoom.scss';
import { usePartyManager } from '../managerContexts';

interface StressInfoProps {
  stress: number;
}

const StressInfo: React.FC<StressInfoProps> = ({ stress }) => {
 
  const partyManager = usePartyManager();
   const colors = partyManager.getStressColors();
  const segments = colors.length;
  const colorIndex = Math.floor(((stress - 1) / 100) * segments);
  const backgroundColor = colors[Math.min(colorIndex, segments - 1)];


  const getStressDescription = (value: number): string => {
    return partyManager.getTensionDescription(value);
  };

  return (
    <div 
      className="stress-info"
      style={{ 
        backgroundColor,
        transition: 'background-color 0.3s ease'
      }}
    >
      <div className="stress-value">{stress}</div>
      <div className="stress-description">
        {getStressDescription(stress)}
      </div>
    </div>
  );
};

export default StressInfo;