import { useEffect, useMemo, useState } from "react";
import GameEncounter from "./gameEncounter";
import { useAppManager, useCampaignManager, useCharacterManager, useUiManager } from "../managerContexts";
import FillBar from "../UI/fillBar";
import NumberDie from "../UI/numderDie";
import { CAMPAIGNS, NB_EXPECTED_ENCOUNTER_TAGS } from "../../consts/campaigns";
import CImage from "../UI/cimage";
import { Tag } from "../../models";
import './dmScreen.scss';
import { APP_STATE } from "../../consts/states";



interface DMScreenProps {

}

const DMScreen: React.FC<DMScreenProps> = () => {

	const campaignManager = useCampaignManager();
  const characterManager = useCharacterManager();
	const encounter = campaignManager.getCurrentEncounter();
  const appManager = useAppManager();
  const uiManager = useUiManager();
  const [collectedTags, setCollectedFlavorTags] = useState(campaignManager.getEstablishedTags());
  const [rumorTags, setRumorTags] = useState(campaignManager.getRumorTags());
  const [locationTagsEstablished, setLocationTagsEstablished] = useState(campaignManager.getWeightedLocationTagsEstablished());
  //const [flavorTagThemes, setFlavorTagThemes] = useState(campaignManager.getCollectedThemes());
  const [backstoryTags, setBackstoryTags] = useState(characterManager.getAllExpectedBackstoryTags());
  const [expectedEncounterTags, setExpectedEncounterTags] = useState(campaignManager.getExpectedEncounterTags());
  const [establishedEncounterTags, setEstablishedEncounterTags] = useState(campaignManager.getResolvedTagsForBranch());
  const [dmPoints, setDMPoints] = useState(campaignManager.getDMPoints());
  const [dmTricks , setDMTricks] = useState(campaignManager.getDmTricks());
	const [locationTags, setLocationTags] = useState(campaignManager.getEncounterLocationTags());
  const [campaignScore, setCampaignScore] = useState(campaignManager.getCampaignScore());
  

  const setSceneTags = useMemo(() => encounter?.location.tags.sort() || [], [encounter?.location.tags]);
  const sortedSetSceneTags = useMemo(() => {
    return [...setSceneTags].sort((a, b) => a.name.localeCompare(b.name));
  }, [setSceneTags]);


  useEffect(() => {
    const updateScreenInfo = () => {
      const newTags = campaignManager.getEstablishedTags();
      const newExpectedEncounterTags = campaignManager.getExpectedEncounterTags();

      setExpectedEncounterTags(newExpectedEncounterTags);
      //filter out tags that are already established
      const newCollectedTags = newTags.filter(tag => 
        !newExpectedEncounterTags.some(collectedTag => collectedTag.name === tag.name) &&
        !establishedEncounterTags.some(establishedTag => establishedTag.name === tag.name));


      setCollectedFlavorTags([...newCollectedTags]); // Ensure a new array reference
      
      
      
      setBackstoryTags(characterManager.getAllExpectedBackstoryTags());
      setDMTricks(campaignManager.getDmTricks());
      setLocationTagsEstablished(campaignManager.getWeightedLocationTagsEstablished());
     
      setRumorTags(campaignManager.getRumorTags());
     // setFlavorTagThemes(campaignManager.getCollectedThemes());
      setDMPoints(campaignManager.getDMPoints());
      setCampaignScore(campaignManager.getCampaignScore());
    };

    campaignManager.on('dmScreenUpdated', updateScreenInfo);

  
    return () => {
      campaignManager.off('dmScreenUpdated', updateScreenInfo);
    };
  }, [campaignManager, characterManager]);


  const encounterBranches = encounter?.branches;


  const renderDMScreenHeader = () => {
    return (
      <div className="dm-screen-header">
       
        <div className="dm-screen-dm-points">
          <div className="dm-screen-dm-points-label">
            DM POINTS
          </div>
          <div className="dm-screen-dm-point">
            <NumberDie baseDie={20} displayNumber={dmPoints} />
          </div>
        </div>
        <div className="dm-screen-campaign">
          <div className={`campaign-display ${CAMPAIGNS[campaignManager.getCurrentCampaign() || 0].theme} icon-only`}> 
            <div className={'campaign-info left'}>
            <div className="campaign-info">{CAMPAIGNS[campaignManager.getCurrentCampaign() || 0].name}</div>
            <div className="campaign-info hooks">PLOT HOOKS</div>
            <div className="circle-display">
              {Array.from({ length: 4 }, (_, index) => (
                <div
                  key={index}
                  className={`circle ${index < campaignManager.getCampaignProgress() ? 'filled' : ''}`}
                />
              ))}
            </div>
          </div>
            
            <div className="campaign-icon solo">
              <CImage imageId={CAMPAIGNS[campaignManager.getCurrentCampaign() || 0].icon} />
            </div>
          </div>
        </div>
        <div className="score-info">
          <div className="score-label">CAMPAIGN SCORE</div>
					<div className="score-value">{campaignScore}</div>	
				</div>
      </div>
    )
  }

  const getPlayerAttitudesStyle = (tag: Tag) => {
    if(showPlayerAttitude) {
      return uiManager.getPlayerAttitudesStyle(tag);
    }
    return '';
  }

  if(!encounter) {
    return null;
  }

  const currentLocationTheme = encounter.location.themes[0];

  const showPlayerAttitude = false;
 

  return (
	<div className = {`dm-screen-container ${appManager.state !== APP_STATE.SESSION ? 'hidden' : ''}`} >
    {renderDMScreenHeader()}

 

    <div className="dm-screen-set-scene">
      {<FillBar totalSegments={locationTags.length} filledSegments={locationTagsEstablished} theme={currentLocationTheme} label={`Set the Scene: ${encounter?.location.name} `} />}
      <div className="dm-screen-set-scene-tags">
        {sortedSetSceneTags.map((tag, index) => (
          <div key={index} className={`encounter-flavor-tag text-pill  ${campaignManager.getCollectedLocationTags().includes(tag) ? 'type-location': ''}`}>
            {tag.name}
          </div>
        ))}  
      </div>
    </div>
    <div className="dm-screen-set-scene">
      {<FillBar totalSegments={establishedEncounterTags.length + expectedEncounterTags.length} filledSegments={establishedEncounterTags.length} theme={currentLocationTheme} label={`encounter elements`} />}
        <div className="dm-screen-encounter-tags-row">
        
          <div className="dm-screen-set-scene-tags">
            {establishedEncounterTags.map((tag, index) => (
              <div key={index} className={`encounter-flavor-tag text-pill type-location`}>
                {tag.name}
              </div>
            ))}  
          </div>
          <div className="dm-screen-set-scene-tags">
            {expectedEncounterTags.map((tag, index) => (
              <div key={index} className={`encounter-flavor-tag text-pill`}>
                {tag.name}
              </div>
            ))}  
          </div>
        </div>
      </div>
    
    {encounterBranches && encounterBranches.length > 0 && (
        <div className="dm-screen-branches-container">
          <div className={`dm-screen-branches-header text-theme-${currentLocationTheme} `}>PREP FOR UPCOMING ENCOUNTERS</div>
          <div className="dm-screen-upcoming-branches">
            {encounterBranches.map((branch, index) => (
              <div key={index} className={`dm-screen-branch text-theme-${branch.location.themes[0]}`}>
                <FillBar totalSegments={4} filledSegments={Math.min(4 ,campaignManager.getExpectedTagsForBranch(branch.name).length)}  theme={branch.location.themes[0]} label={branch.name} />
                <div className="dm-screen-branch-tags">
                {(() => {
                  
                  const expectedTags = campaignManager.getExpectedTagsForBranch(branch.name);
                  const authoredTags = campaignManager.getAuthoredEncounterTags(branch.name).filter((tag) => !expectedTags.includes(tag));
                  const totalSlots = NB_EXPECTED_ENCOUNTER_TAGS - (authoredTags.length + expectedTags.length);
                  const fillerTags = Array(totalSlots)
                    .fill({ name: "?" });
                  
                  return [...authoredTags.map((tag, index) => (
                    <div key={`authored-${index}`} className={`encounter-flavor-tag text-pill expected`}>
                      {tag.name}
                    </div>
                  )),
                    ...expectedTags.map((tag, index) => (
                    <div key={`expected-${index}`} className={`encounter-flavor-tag text-pill type-foreshadowing`}>
                      {tag.name}
                    </div>
                  )), ...fillerTags.map((tag, index) => (
                    <div key={`filler-${index}`} className={`encounter-flavor-tag text-pill type-foreshadowing expected large-text`}>
                    {tag.name}
                  </div>
                  ))]
                })()}
                </div>
              </div>
            ))}
          </div>
        </div>
    )}
    


        {collectedTags.length > 0 && 
        <div className = "dm-screen-collected-tags">
          <div className="dm-screen-collected-tags-header">Established Elements</div>
          <div className="dm-screen-collected-tags-container">
            {collectedTags.map((tag, index) => (
              <div key={index} className={`encounter-flavor-tag text-pill  ${getPlayerAttitudesStyle(tag)} established`}>
                {tag.name}
              </div>
            ))}
          </div>
        </div>}
      {rumorTags.length > 0 && 
      <div className="dm-screen-expected-tags">
          <div className="dm-screen-collected-tags-header">Rumors</div>
          <div className="dm-screen-collected-tags-container">
            {rumorTags.map((tag, index) => (
            <div key={index} className={`encounter-flavor-tag text-pill ${getPlayerAttitudesStyle(tag)} expected`}>
              {tag.name}
            </div>
            ))}
          </div>
      </div>
      }
      {/* {expectedEncounterTags.length > 0 && 
      <div className="dm-screen-expected-tags">
          <div className="dm-screen-collected-tags-header">Elements Expected By Players</div>
          <div className="dm-screen-collected-tags-container">
            {expectedEncounterTags.map((tag, index) => (
            <div key={index} className={`encounter-flavor-tag text-pill ${getPlayerAttitudesStyle(tag)} expected`}>
              {tag.name}
            </div>
            ))}
          </div>
      </div>
      } */}
      {backstoryTags.length > 0 && 
      <div className="dm-screen-expected-tags">
          <div className="dm-screen-collected-tags-header">Elements needed to advance a Character's Backstory</div>
          <div className="dm-screen-collected-tags-container">
            {backstoryTags.map((tag, index) => (
            <div key={index} className={`encounter-flavor-tag text-pill ${getPlayerAttitudesStyle(tag)} expected`}>
              {tag.name}
            </div>
            ))}
          </div>
      </div>
      }
       {/* {dmTricks.length > 0 && 
      <div className="dm-screen-expected-tags">
          <div className="dm-screen-collected-tags-header">Available DM Powers</div>
          <div className="dm-screen-collected-tags-container">
            {dmTricks.map((tag, index) => (
            <div key={index} className={`encounter-flavor-tag text-pill type-${tag.source}`}>
              {tag.tag.name}
            </div>
            ))}
          </div>
      </div>
      } */}
   
	</div>
  );
}

export default DMScreen;