import { useEffect, useRef, useState } from "react";
import { useAppManager } from "../managerContexts";
import { CombatState, Tag } from "../../models";
import RangeCard from "./rangeCard";
import CombatModifiers from "./combatModifiers";
import ModeContainer from "../modecontainer";
import AddCombatants from "./addCombatants";
import { THEME_FILLED_REQ } from "../../consts/general";


interface ChallengeBarProps      {
  results: number;
  resultsValues: number[][];
}




const barLabels = ['Score', 'Tension on fail', 'Expected Reward'];


const ChallengeBar: React.FC<ChallengeBarProps> = ({results, resultsValues}) => {


   
    const challengeBarArrayLength = resultsValues[0].length;
    const appManager = useAppManager();
    const combatManager = appManager.getCombatManager();
    const partyManager = appManager.getPartyManager();
    const chatManager = appManager.getChatManager();

    const campaignManager = appManager.getCampaignManager();
    
    const themes = campaignManager.getCollectedThemes();
 
    const [engagementLabels, setEngagementLabels] = useState<number[][]>(resultsValues);
  
    const [highlightedSegment, setHighlightedSegment] = useState<number>(results);
   
    const segmentWidth = 100 / challengeBarArrayLength;
    const arrowPosition = `${highlightedSegment * segmentWidth + segmentWidth/2}%`;

   

    
    useEffect(() => {
        setHighlightedSegment(results)
    }, [results]);

    const generateColorsFromLabels = (numbers: number[],index: number): string[] => {
        const themes = ['engagement','stress', 'reward'];
        
        const theme = themes[index];

        const min = Math.min(...numbers);
        const max = Math.max(...numbers);
        return numbers.map(n => combatManager.generateColorFromValue(n, min, max, theme));
    };
      
    
    const getSegments = (index: number) => {


const reward = index === 2;
        const segmentColors = generateColorsFromLabels(engagementLabels[index], index);
        const labels = engagementLabels[index];
        const segments = Array.from({ length: challengeBarArrayLength }, (_, index) => (
            <div
            key={index}
            className={
                `challenge-bar-segment  
                ${index === highlightedSegment ? 'highlighted' : ''}
                ${reward ? 'reward' : ''}
            `}
            style={{ 
                backgroundColor: segmentColors[index] || '',
                
            }}
            ><div className="label">{labels[index]}</div></div>
        ));
        return (
            <div className = "challenge-bar">
                <div className="segment-container">{segments}</div>
            </div>);
    }


    return (
        <div className="challenge-reward-row">
            <div className="challenge-bar-container">
                <div className="challenge-bar-results-label">RESULTS</div>
                <div className="challenge-labels">
                    {barLabels.map((label, index) => (
                        <div key={index} className="challenge-label">
                            {label}
                        </div>
                    ))}
                </div>
                <div className="challenge-bars">
                    <div className="segment-numbers">
                        {Array.from({ length: challengeBarArrayLength }, (_, i) => (
                        <div key={i} className="segment-number">{i}</div>
                        ))}
                    </div>
                    <div className="challenge-arrow" style={{ left: arrowPosition }} />
                    {engagementLabels.map((_l, index) => (
                        <div className="segment-instance" key={index}>{getSegments(index)}</div>
                    ))}
                    </div>
                </div>
            </div>
);
}
export default ChallengeBar; 