import { useEffect, useState } from "react";
import { Tag, HeroCombatant, ATTRIBUTE_TYPE, ATTRIBUTE_CONTRIBUTOR, EncounterFlavorTag } from "../../models";
import { useCampaignManager, useCharacterManager, usePartyManager, usePlayerManager } from "../managerContexts";
import { getAttributesForTag } from "../../utils/tagUtils";
import { JSX } from "react/jsx-runtime";
import AttributeDisplay from "../UI/attributeDisplay";
import { getCombinedAttributes } from '../../consts/characterConsts';
import { clamp } from "../../utils/utils";
import { COST, SCORE } from "../../consts/score";

interface ChallengeDisplayProps {
    challengeTags: Tag[];
    challengeAssistTags: Tag[];
    challengeHeroes: HeroCombatant[];
    attemptCallback: (success: boolean,result: number, currentTag: Tag) => void;
    sawResultsCallback: () => void;
    heroesEnabledCallback: (enabled: boolean) => void;
}

const ChallengeDisplay: React.FC<ChallengeDisplayProps> = ({
    challengeAssistTags,
    challengeHeroes,
    challengeTags,
    attemptCallback,
    sawResultsCallback,
    heroesEnabledCallback,
}) => {
    const characterManager = useCharacterManager();
    const campaignManager = useCampaignManager();
    const partyManager = usePartyManager();
    const playerManager = usePlayerManager();
    const [currentDifficulty, setCurrentDifficulty] = useState(1);
    const [currentTagIndex, setCurrentTagIndex] = useState(0);

    const [dieResult, setDieResult] = useState(0);
    const [showResult, setShowResult] = useState(false);
    const [completedChallenges, setCompletedChallenges] = useState<boolean[]>(
        Array(challengeTags.length).fill(false)
    );
    const [resultFailed, setResultFailed] = useState(false);

    const allChallengesCompleted = completedChallenges.every((completed) => completed);

    const generateTagIcons = (tag: Tag, multiplier: number) => {
        let icons: JSX.Element[] = [];
        let symbol;
        let attributes = getAttributesForTag(tag);

        const attributeKeys = Object.keys(attributes) as (keyof typeof attributes)[];

        attributeKeys.forEach((key) => {
            if (attributes[key]) {
                for (let i = 0; i < attributes[key] * multiplier; i++) {
                    symbol = characterManager.getUFAttributeSymbol(key as ATTRIBUTE_TYPE);
                    icons.push(
                        <span
                            key={`${key as ATTRIBUTE_TYPE}-${i}`}
                            className={`attribute-icon ${key as ATTRIBUTE_TYPE}`}
                        >
                            {symbol}
                        </span>
                    );
                }
            }
        });
        return icons;
    };

    const isTagEstablished = (tag: Tag): boolean => {
        return campaignManager.getEstablishedTags().some((establishedTag) => establishedTag.name === tag.name) || 
               campaignManager.getCollectedLocationTags().some((establishedTag) => establishedTag.name === tag.name);
    };

    useEffect(() => {
        setShowResult(false);
        if(completedChallenges[currentTagIndex]) { 
            heroesEnabledCallback(false);
        }else{
            heroesEnabledCallback(true);
        }

        if (!isTagEstablished(challengeTags[currentTagIndex])) {
            setCurrentDifficulty(0);
        } else {
            if (currentDifficulty === 0) {
                setCurrentDifficulty(1);
            }
        }
    }, [currentTagIndex, completedChallenges]);

    const heros = (hero?: HeroCombatant) => {
        if (!hero)
            return (
                <div className="challenge-assistance-hero no-hero">
                    Ask a player to select a hero to attempt the challenge.
                </div>
            );

        return (
            <div className="challenge-assistance-hero">
                <div className="challenge-assistance-hero-name">{hero.name}</div>
                <AttributeDisplay
                    hero={hero}
                    collapsed={true}
                    showAttributeLoss={false}
                    category={ATTRIBUTE_CONTRIBUTOR.ALL}
                />
            </div>
        );
    };

    const getAllAssistAttributes = (
        heroes: HeroCombatant[],
        challengeAssistTags: Tag[]
    ) => {
        let combinedAttributes: { [key in ATTRIBUTE_TYPE]?: number } = {};
        heroes.forEach((hero) => {
            if (hero.npcTag) {
                hero = characterManager.generateNPC(hero.npcTag);
            }

            let heroAttributes = getCombinedAttributes(hero.character, true);
            let heroAttributeKeys = Object.keys(
                heroAttributes
            ) as (keyof typeof heroAttributes)[];
            heroAttributeKeys.forEach((key) => {
                if (combinedAttributes[key]) {
                    combinedAttributes[key] += heroAttributes[key];
                } else {
                    combinedAttributes[key] = heroAttributes[key];
                }
            });
        });

        challengeAssistTags.forEach((tag) => {
            let tagAttributes = getAttributesForTag(tag);
            let tagAttributeKeys = Object.keys(
                tagAttributes
            ) as (keyof typeof tagAttributes)[];
            tagAttributeKeys.forEach((key) => {
                if (combinedAttributes[key] && tagAttributes[key]) {
                    combinedAttributes[key] =
                        (combinedAttributes[key] || 0) + tagAttributes[key];
                } else {
                    combinedAttributes[key] = tagAttributes[key];
                }
            });
        });
        return combinedAttributes;
    };

    const calculateSuccess = (
        currentTag: Tag,
        difficulty: number,
        heroes: HeroCombatant[],
        challengeAssistTags: Tag[]
    ) => {

        if(heroes.length === 0) {
            return {
                base: 0,
                combat: 0,
                magic: 0,
                tools: 0,
                total: 0
            }
        }
  

        let challengeAttributes = getAttributesForTag(currentTag);

        let assistAttributes = getAllAssistAttributes(heroes, challengeAssistTags);

        let baseChance = 75; // campaignManager.getBaseFromWhateverThemesOrPlayerStuffIDontKnow
        const multiplier = difficulty + 1;

        let combat =
            (challengeAttributes[ATTRIBUTE_TYPE.COMBAT] || 0) * (multiplier || 1);
        let magic =
            (challengeAttributes[ATTRIBUTE_TYPE.MAGIC] || 0) * (multiplier || 1);
        let tools =
            (challengeAttributes[ATTRIBUTE_TYPE.TOOLS] || 0) * (multiplier || 1);

        let assistCombat = assistAttributes[ATTRIBUTE_TYPE.COMBAT] || 0;
        let assistMagic = assistAttributes[ATTRIBUTE_TYPE.MAGIC] || 0;
        let assistTools = assistAttributes[ATTRIBUTE_TYPE.TOOLS] || 0;

        let combatChance = combat === 0 ? 0 : (combat - assistCombat) * -5;
        let magicChance = magic === 0 ? 0 : (magic - assistMagic) * -5;
        let toolsChance = tools === 0 ? 0 : (tools - assistTools) * -5;

        let totalChance = clamp(
            baseChance + combatChance + magicChance + toolsChance,
            5,
            95
        );

        return {
            base: baseChance,
            combat: combatChance,
            magic: magicChance,
            tools: toolsChance,
            total: totalChance,
        };
    };


    const attemptChallenge = (
        currentTag: Tag,
        difficulty: number,
        heroes: HeroCombatant[],
        challengeAssistTags: Tag[]
    ) => {
        heroesEnabledCallback(false);
        partyManager.updateAttention(
            heroes
                .map((hero) => {
                    if (hero.character) {
                        return playerManager.getPlayerAssignedCharacter(hero.character.id)?.name;
                    }
                    return '';
                })
                .filter((name): name is string => name !== undefined && name !== '')
        );
        const dieRoll = Math.ceil(Math.random() * 100);
        setDieResult(dieRoll);
        const calcSuccess = calculateSuccess(
            currentTag,
            difficulty,
            heroes,
            challengeAssistTags
        );

        partyManager.addPartyScore(SCORE.SKILL_CHALLENGE_ELEMENT)

        const success = dieRoll <= calcSuccess.total;
        if (success) {
            setResultFailed(false);
            attemptCallback(success, difficulty, currentTag);
            if(difficulty > 0) {
                let newCompletedChallenges = [...completedChallenges];
                newCompletedChallenges[currentTagIndex] = true;
                setCompletedChallenges(newCompletedChallenges);
            } else {
                campaignManager.resolveExpectedTagsForBranch(currentTag);
            }

        } else {
            setResultFailed(true);
            attemptCallback(false, difficulty, currentTag);
        }
        setShowResult(true);
    };

    useEffect(() => {
        
    }, [showResult])

    const handleResultAccept = () => {
        setShowResult(false);
        if(currentDifficulty === 0 && !resultFailed) {
            setCurrentDifficulty(1);
        }
        sawResultsCallback();
    }

    const getDifficultyText = (difficulty: number) => {
        switch (difficulty) {
            case 0:
                return "Establishing";
            case 1:
                return "Easy";
            case 2:
                return "Challenging";
            case 3:
                return "Difficult";
            default:
                return "";
        }
    };

    const getDifficultyRewardText = (difficulty: number) => {
        switch (difficulty) {
            case 0:
                return "";
            case 1:
                return "+1 result";
            case 2:
                return "+2 result";
            case 3:
                return "+3 result";
            default:
                return "";
        }
    };

    const clickNext = () => {
        if (currentTagIndex < challengeTags.length - 1) {
            setCurrentTagIndex(currentTagIndex + 1);
        } else {
            setCurrentTagIndex(0);
           
        }
         sawResultsCallback();
    };
    const clickPrev = () => {
        if (currentTagIndex > 0) {
            setCurrentTagIndex(currentTagIndex - 1);
        } else {
            setCurrentTagIndex(challengeTags.length - 1);
        }
        sawResultsCallback()
    };
    const clickDifficultyUp = () => {
        if (currentDifficulty === 3) {
            setCurrentDifficulty(1);
        } else {
            setCurrentDifficulty(currentDifficulty + 1);
        }
    };
    const clickDifficultyDown = () => {
        if (currentDifficulty === 1) {
            setCurrentDifficulty(3);
        } else {
            setCurrentDifficulty(currentDifficulty - 1);
        }
    };

    const renderLegend = (
        currentTag: Tag,
        difficulty: number,
        heroes: HeroCombatant[],
        challengeAssistTags: Tag[]
    ) => {
        const difficultyRating = calculateSuccess(
            currentTag,
            difficulty,
            heroes,
            challengeAssistTags
        );

        return (
            <div className="challenge-display-footer-legend-container">
                <div className="challenge-display-legend-info">
                    <div className="challenge-display-legend-item">
                        <u>Base</u>
                        <br />
                        {difficultyRating.base}%
                        <br />
                    </div>
                    <div className="challenge-display-legend-item">
                        <u>⚔️</u>
                        <br />
                        {difficultyRating.combat < 0 ? "" : "+"}
                        {difficultyRating.combat}%
                        <br />
                    </div>
                    <div className="challenge-display-legend-item">
                        <u>✨</u>
                        <br />
                        {difficultyRating.magic < 0 ? "" : "+"}
                        {difficultyRating.magic}%
                        <br />
                    </div>
                    <div className="challenge-display-legend-item">
                        <u>🔧</u>
                        <br />
                        {difficultyRating.tools < 0 ? "" : "+"}
                        {difficultyRating.tools}%
                        <br />
                    </div>
                </div>
                <div className="challenge-display-legend-info">
                    <div className="challenge-display-legend-item">
                        +5 Exhaustion / Hero
                    </div>
                  
                </div>
                <div className="challenge-display-legend-info">
                <div className="challenge-display-legend-item">
    +10 Score / Location Element
</div>

                  
                </div>
                   
            </div>
        );
    };
    <div className="challenge-display-legend-item">
    +10 Score / Location Element
</div>


    const disableChallengeButton = () => {
        return (
            completedChallenges[currentTagIndex] || 
            challengeHeroes.length === 0
        );
    };


    return (
        <div className="challenge-display-column">
            <div className="challenge-tag-container">
                <div className="challenge-tag-ui prev" onClick={clickPrev}>
                    <div className="challenge-tag-ui-text">{"<"}</div>
                </div>
                <div
                    className={`challenge-current-tag text-pill theme-${challengeTags[currentTagIndex]} ${
                        isTagEstablished(challengeTags[currentTagIndex])
                            ? " type-encounter"
                            : "expected"
                    } `}
                >
                    {challengeTags[currentTagIndex]?.name}
                </div>
                <div className="challenge-tag-ui next" onClick={clickNext}>
                    <div className="challenge-tag-ui-text">{">"}</div>
                </div>
            </div>

            {completedChallenges[currentTagIndex] && !showResult ? (
                <div>
                    <div className ="challenge-succeeded-instructions">{
                    allChallengesCompleted ? 'Players have completed all Challenges. Reward them.' :
                    'Continue the challenge with another element or reward the players and move on.'
                    }</div>
                    <div className="challenge-succeeded">CHALLENGE SUCCEEDED</div>
                </div>
            ) :(
            <div className = "challenge-display-container">
                <div className="challenge-display">
                    <div className="challenge-display-row">
                        {currentDifficulty > 0 && (
                            <div
                                className="challenge-difficulty-ui difficulty-down"
                                onClick={clickDifficultyDown}
                            >
                                <div className="challenge-difficulty-ui-text">-</div>
                            </div>
                        )}

                        <div className="challenge-display-header-column">
                            <div className="challenge-display-instructions">
                                {currentDifficulty === 0
                                    ? `Players haven't been exposed to ${challengeTags[currentTagIndex].name} yet. Introduce the new topic with a simple challenge.`
                                    : "Set the challenge rating and raise or lower the expected reward."}
                            </div>

                            <div className="challenge-tag-attribute-container">
                                {generateTagIcons(challengeTags[currentTagIndex], currentDifficulty + 1)}
                            </div>
                            <div className="challenge-difficulty-display-difficulty">
                                {currentDifficulty > 0 && (
                                    <div>{getDifficultyText(currentDifficulty)} </div>
                                )}
                                <div>{getDifficultyRewardText(currentDifficulty)} </div>
                            </div>
                        </div>

                        {currentDifficulty > 0 && (
                            <div
                                className="challenge-difficulty-ui difficulty-up"
                                onClick={clickDifficultyUp}
                            >
                                <div className="challenge-difficulty-ui-text">+</div>
                            </div>
                        )}
                    </div>
                </div>
                <div className="challenge-assistance-container">
                    <div className="challenge-assistance-list">
                        <div className="challenge-assistance-list-column">
                            {challengeHeroes.length > 0 ? (
                                challengeHeroes.map((hero, index) => (
                                    <div key={hero.name || index}>{heros(hero)}</div>
                                ))
                            ) : (
                                <div className="challenge-assistance-list-no-heroes">
                                    {heros()}
                                </div>
                            )}
                        </div>
                        <div className="challenge-assistance-list-column">
                            {challengeAssistTags.length > 0
                                ? challengeAssistTags.map((tag, index2) => (
                                        <div key={index2} className="challenge-assistance-tag">
                                            <div className="challenge-assistance-tag-name-container">
                                                <div className="challenge-assistance-tag-name text-pill type-location">
                                                    {tag?.name}
                                                </div>
                                            </div>
                                            {tag && generateTagIcons(tag, 1)}
                                        </div>
                                    ))
                                : null}
                        </div>
                    </div>
                </div>
                <div className="challenge-display-footer">
                        <div className="challenge-attempt-attempt-row">
                            {renderLegend(
                                challengeTags[currentTagIndex],
                                currentDifficulty,
                                challengeHeroes,
                                challengeAssistTags
                            )}
                            {showResult ? (
                                <div className="challenge-result-container">
                                    <div className="challenge-result-header">
                                        <u>{resultFailed ? "FAILED" : currentDifficulty === 0 ? "Established" : "SUCCEEDED"}</u>
                                    </div>
                                   
                                    <div className="challenge-result-info">
                                        <div className="challenge-result-labels">
                                            {['Needed:','Rolled:'].map((label, index) => (
                                                <div key={index} className="result-label">
                                                {label}
                                            </div>
                                            ))}   
                                        </div>     
                                        <div className="challenge-result-values">
                                            {[   calculateSuccess(
                                                    challengeTags[currentTagIndex],
                                                    currentDifficulty,
                                                    challengeHeroes,
                                                    challengeAssistTags
                                                ).total
                                            ,dieResult].map((label, index) => (
                                                <div key={index} className="result-label">
                                                    {label}
                                                </div>
                                            ))}   
                                        </div>   
                                    </div>  
                                    <div className="challenge-result-button" onClick={() => handleResultAccept()}>
                                        OK
                                    </div>     
                                                          
                                </div>


                            ) : (
                            <div
                                className={`challenge-attempt-button ${
                                    completedChallenges[currentTagIndex] ? "completed" : ""
                                }
                                ${disableChallengeButton() ? "disabled" : ""}`}
                                onClick={() =>
                                    attemptChallenge(
                                        challengeTags[currentTagIndex],
                                        currentDifficulty,
                                        challengeHeroes,
                                        challengeAssistTags
                                    )
                                }
                            >
                                <div>
                                    {currentDifficulty === 0 ? 'ESTABLISH' : 'ATTEMPT'}
                                    <div className="challenge-attempt-button-success">
                                        {
                                            calculateSuccess(
                                                challengeTags[currentTagIndex],
                                                currentDifficulty,
                                                challengeHeroes,
                                                challengeAssistTags
                                            ).total
                                        }
                                        %
                                    </div>
                                    <div className="challenge-attempt-exhaustion">
                                        {`${COST.EXHAUSTION.CHALLENGE_PER_HERO* (challengeHeroes.length || 1)} Exhaustion`}
                                    </div>
                                </div>
                           
                            </div>
                         )}
                        </div>
                   
                </div>
            </div>
            )}
        </div>
    );
};

export default ChallengeDisplay;