import React from 'react';
import './notebook.scss'; 
import { LocationNotebookEntry } from '../../models';
import CImage from './cimage';


interface NoteBookLocationProps {
    locationInfo: LocationNotebookEntry;
}

const NotebookLocation: React.FC<NoteBookLocationProps> = ({ locationInfo }) => {
    const location = locationInfo.location;


    return (
        <div className="notebook-location-container">
        <div className="notebook-location-contents left">
            <div className="notebook-text location-description">
                <b>Description:</b> {location.description}
            </div>
        </div>
        <div className="notebook-location-contents right">
            <div className="elements-features-list-container">
                {location.tags.map((tag, index) => {
                    return <div key={index}  className="notebook-text elements-features-list text-pill tag"> - {tag.name} </div>
                })}
            </div>
            <div className="notebook-icon-container">
                <CImage imageId={location.icon} />
            </div>
        </div>
    </div>
    );
};

export default NotebookLocation;