

import './gameRoom.scss';

import { Encounter, EncounterFlavorTag, Tag } from '../../models';
import { useState } from 'react';
import { useCampaignManager, useUiManager } from '../managerContexts';



interface GameEncounterIntroProps {
	encounter: Encounter;
}

const GameEncounterIntro: React.FC<GameEncounterIntroProps> = ({ encounter }) => {

	const uiManager = useUiManager();
	const campaignManager = useCampaignManager();
	const [expectedEncounterTags, ] = useState<Tag[]>(campaignManager.getExpectedEncounterTags());
	const [locationsTags, ] = useState<Tag[]>(campaignManager.getEncounterLocationTags());
	const [dmTricks, ] = useState<EncounterFlavorTag[]>(campaignManager.getDmTricks());

	  const instructions_plot = "Keep your players thinking about what's to come by giving clues about upcoming encounters, and the Campaign Plot";
	  const instructions_expected_prev = "Based on what you've told them about the current situation in Sagewood, they expect to encounter:";
	  const instructions_set_scene = 'Most of this encounter takes place in Sagewood Tavern. Describe the scene to them by exposing the following elements:';
	  const dm_tricks = 'Elements you have pre readied (miniatures, maps, etc.) to use for improvisation:';


	  const gameElements = [campaignManager.getCampaignName(), ...(campaignManager.getCurrentEncounter()?.branches?.map((branch) => branch.name) || [])];



	  return (
		<div className="encounter-intro-container">
		  	<div className="encounter-intro-title">{encounter.name}</div>
		  	<div className="encounter-intro-instructions">{instructions_set_scene}</div>
			<div className="encounter-intro-location">{encounter.location.name}</div>
		
			<div className="encounter-intro-tags location">
			{locationsTags.map((tag, index) => (
				<div
				key={index}
				className={`encounter-flavor-tag text-pill type-location`}
				>
				{tag.name}
				</div>
			))}
			</div>
	
		<div className="encounter-intro-instructions expect">{instructions_expected_prev}</div>
		{expectedEncounterTags.length > 0 &&	(
			<div>
		 
			<div className="encounter-intro-tags expected">
			{expectedEncounterTags.map((tag, index) => (
				<div
					key={index}
					className={`encounter-flavor-tag text-pill expected`}
				>
					{tag.name}
				</div>
				))}
				</div>
			</div>
		)}

		<div className="encounter-intro-instructions plot">{instructions_plot}</div>
		  <div className="encounter-intro-tags plot">
		  {gameElements.map((name, index) => (
			  <div
				key={index}
				className={`text-encounter text-theme-${campaignManager.getColorForEncounterByName(name)} `}
			  >
			<u>	{name}</u>
			  </div>
			))}

		  </div>
		 
		
		
		
	  </div>
	  );
	};

export default GameEncounterIntro;