import React from 'react';
import './modal.css'; // Assuming you have a CSS file for basic styling

interface FillBarProps {
    totalSegments: number;
    filledSegments: number;
    theme?: string;
    label?: string;
    buttonWhenFilled?: boolean;
    onClickFilled?: () => void;
    filledLabel?: string;
    labelStyle?: 'over' | 'normal';
}

const FillBar: React.FC<FillBarProps> = ({ totalSegments, filledSegments, theme = '', label, buttonWhenFilled = false, onClickFilled, filledLabel, labelStyle = 'normal' }) => {
    
    const segments = Array.from({ length: totalSegments }, (_, index) => (
        <div
          key={index}
          className={`fill-bar-segment bg-theme-${theme} ${index < filledSegments ? 'filled' : ''}`}
        />
      ));
      
    const filled = filledSegments >= totalSegments;
    const themeClass = 'theme-' + theme;
    if(labelStyle !== 'over') {
        return ( 
            <div className={`fill-bar`}>
                {label && <div className={`fill-bar-label text-theme-${theme || ''}`}>{label}</div>}
                <div className={`fill-bar-container ${filled ? themeClass : ''}`}>
                    {filled && buttonWhenFilled ? (
                        <div className={`fill-bar-filled-button ${themeClass}`} onClick={onClickFilled}>{filledLabel}</div>
                     ) : (
                     segments
                    )
                    }
                </div>
            </div>
            )
    }


 
    return (
        <div className={`fill-bar`}>
          <div className={`fill-bar-container ${filled ? themeClass : ''}`}>
            {label && labelStyle === 'over' && (
              <div className={`fill-bar-label-over text-theme-${theme || ''}`}>
                {label}
              </div>
            )}
            {filled && buttonWhenFilled ? (
              <div className={`fill-bar-filled-button ${themeClass}`} onClick={onClickFilled}>
                {filledLabel}
              </div>
            ) : (
              segments
            )}
          </div>
        </div>
      );
};
    
export default FillBar;
