interface NeedleGaugeProps {
    value: number;
    max: number;
    segments?: number;
    colors?: string[];
  }
  
  const NeedleGauge: React.FC<NeedleGaugeProps> = ({ 
    value, 
    max, 
    segments = 10,
    colors = ['#FFA500', '#FFD700','#FFD700', '#ADFF2F', '#ADFF2F', '#ADFF2F', '#32CD32','#eF4500', '#eF4500','#FF0000']
  }) => {
    const radius = 100;
    const tickLength = 20;
    const colorLength = 20;
    const angleRange = 180; // Half circle
    let currentAngle = (value / max) * angleRange;
    
    const generateTicks = () => {
      currentAngle = (value / max) * angleRange;
      const ticks = [];
      for (let i = 0; i <= segments; i++) {
        const angle = (i / segments) * angleRange;
        const angleRad = (angle + 180) * (Math.PI / 180);
        const x1 = radius * Math.cos(angleRad) + radius;
        const y1 = radius * Math.sin(angleRad) + radius;
        const x2 = (radius - tickLength) * Math.cos(angleRad) + radius;
        const y2 = (radius - tickLength) * Math.sin(angleRad) + radius;
        
        ticks.push(
          <line
            key={i}
            x1={x1}
            y1={y1}
            x2={x2}
            y2={y2}
            stroke={[1,3,6,7,9].includes(i) ? "black" : "#00000000"}
            strokeWidth="2"
          />
        );
      }
      return ticks;
    };
  
    const generateArcs = () => {
      const arcs = [];
      const segmentAngle = angleRange / colors.length;
      
      for (let i = 0; i < colors.length; i++) {
        const startAngle = -90 + (i * segmentAngle);
        const endAngle = startAngle + segmentAngle;
        
        const pathData = describeArc(radius, radius, radius - colorLength/2, startAngle, endAngle);
        arcs.push(
          <path
            key={i}
            d={pathData}
            fill="none"
            stroke={colors[i]}
            strokeWidth={colorLength}
          />
        );
      }
      return arcs;
    };
  
    const needleStyle = {
      transform: `rotate(${currentAngle - 180}deg)`,
      transformOrigin: '100px 100px'
    };
  
    return (
        <div className="needle-gauge">
        <svg viewBox="20 50 1 160" preserveAspectRatio="xMidYMid meet">
          {generateArcs()}
          {generateTicks()}
         
          <line
            x1={radius}
            y1={radius}
            x2={radius * 2}
            y2={radius}
            stroke="#000000bb"
            strokeWidth="5"
            style={needleStyle}
          />
          <circle
            cx={radius}
            cy={radius}
            r="5"
            fill="white"
          />
        </svg>
      </div>
    );
  };
  
  // Helper function to generate SVG arc path
  const describeArc = (x: number, y: number, radius: number, startAngle: number, endAngle: number) => {
    const start = polarToCartesian(x, y, radius, endAngle);
    const end = polarToCartesian(x, y, radius, startAngle);
    const largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1";
    
    return [
      "M", start.x, start.y,
      "A", radius, radius, 0, largeArcFlag, 0, end.x, end.y
    ].join(" ");
  };
  
  const polarToCartesian = (centerX: number, centerY: number, radius: number, angleInDegrees: number) => {
    const angleInRadians = (angleInDegrees - 90) * Math.PI / 180.0;
    return {
      x: centerX + (radius * Math.cos(angleInRadians)),
      y: centerY + (radius * Math.sin(angleInRadians))
    };
  };
  
  export default NeedleGauge;