import { Attributes } from "react";
import { Attribute, Tag } from "../models";
import { TAG } from "../consts/tags";



export const getAttributesForTag = (tag: Tag): Attribute => {
    if(tag.attributeDefault) {
        switch(tag.attributeDefault) {
            case 'furniture':
                return {
                    combat: 1,
                    tools: 2
                };
            case 'food':
                return {
                    tools: 2
                }
            default: {
                return {
                    tools: 1
                };
            }
        }
    } else {
        switch(tag) {
            case TAG.RACE.HUMANS:
                return {
                    combat: 2,
                    tools: 2,
                };
            case TAG.RACE.HALFLINGS:
                return {
                    combat: 1,
                    tools: 2,
                    magic: 1
                };
            case TAG.EVENTS.MYSTERIES:
                {
                    return {
                        magic: 1,
                        tools: 1
                    };
                }
                case TAG.MONSTERS.CULTISTS:
                    {
                        return {
                            magic: 3,
                            tools: 1
                        };
                    }
                
        }
    }
    return {};
}